<template>
    <div>
      <div class="video_list">
        <div class="list_head list_head01"><span  class="fs_24 c_ff6e97 fr"> ></span></div>
        <com-scroll ref="relativeScrollX" style="position: relative;" :refreshDelay="300" :scrollX="scrollX" :scrollY="scrollY" >
          <com-content-list-x  :width="3" :scrollX="true" ></com-content-list-x>
        </com-scroll>
        <other-see></other-see>
      </div>
    </div>
</template>
<style>

</style>
<script>
import ComScroll from '@/components/com_scroll.vue'
import ComContentListX from '@/components/com_content_list_x.vue'
import ComContentTitle from '@/components/com_column_title.vue'
import OtherSee from '@/components/com_they_watch.vue'

export default {
  components: {
    ComScroll, ComContentTitle, ComContentListX, OtherSee
  },
  data () {
    return {
      scrollX: true,
      scrollY: false
    }
  },
  methods: {},
  mounted: function () {}
}
</script>



