<template>
  <div>
    <div class="load_text"></div>
    <div @click="gotoHome" class="load_btn"><a></a></div>
  </div>
</template>

<style>
  .guide {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2000;
    background:-webkit-gradient(linear, 0 0, 0 bottom, from(rgba(255, 219, 253, 1)), to(#ffc6f5));
  }
</style>
<script>
/* eslint-disable-next-line */
/* eslint-disable-next-line */
export default {
  data () {
    return {
      t: null
    }
  },
  methods: {
    gotoHome: function () {
      this.$router.push({ path: '/homePage' })
      clearTimeout(this.t)
    }
  },
  mounted: function () {
    this.t = setTimeout(() => this.$router.push({ path: '/login' }), 3000)
  },
  beforeRouteLeave (to, from, next) {
    document.body.removeAttribute('class', 'bg05 loading')
    next()
  }
}
</script>



