/*eslint-disable*/
import * as type from '../type';

const state     = {
  userInfo: JSON.parse(localStorage.getItem('userInfo')) || {},
  userData: JSON.parse(localStorage.getItem('userData')) || {},
  loginStatus: JSON.parse(localStorage.getItem('userStatus')) || false
};
const getters   = {
  userData: state => state.userData,
  userInfo : state => state.userInfo,
  loginStatus: state => state.loginStatus
};
const mutations = {
  [type.SET_USER_INFO](state,res){
    state.userInfo = res
  },
  [type.SET_USER_DATA](state,res){
    state.userData = res
  },
  [type.SET_LOGIN_STATUS](state,status){
    state.loginStatus = status
  }
};
const actions   = {
  setUserInfo({commit}, res){
    localStorage.setItem('userInfo', JSON.stringify(res));
    localStorage.setItem('loginStatus', true);
    commit(type.SET_USER_INFO,res);
    commit(type.SET_LOGIN_STATUS,true);
  },
  setUserData({commit},res){
    localStorage.setItem('userData',JSON.stringify(res));
    commit(type.COM_LOADING_STATUS,false);
    commit(type.SET_USER_DATA,res);
  },
  setSignOut({commit}){
    localStorage.removeItem('userInfo');
    localStorage.removeItem('userData');
    localStorage.removeItem('loginStatus');
    commit(type.SET_LOGIN_STATUS,false);
    commit(type.SET_USER_INFO,{});
    commit(type.SET_USER_DATA,{});
    commit(type.COM_PRE_STATE_CLEAR);
    commit(type.COM_CURRENT_FEATURE,'learnToSing');
    commit(type.COM_CURRENT_FID,43);
    commit(type.COM_CURRENT_CATEGORY,'chinese');
    commit(type.COM_CURRENT_LEVEL,'1');
  }
};


export  default {
  state,
  getters,
  mutations,
  actions
}



