<template>
  <div class="study">
    <section class="section  study container" style="display: flex ;flex-wrap: wrap">
      <div v-if="mode==='posttime'?true:false" class="list_head list_head03 fs_24 c_ff6e97"> ></div>
      <div v-else-if="mode==='hits'?true:false" class="list_head list_head04 fs_24 c_ff6e97"> ></div>
      <div  v-else-if="mode==='sort'?true:false" class="fenlei" style="width: 7.5rem">
        <ul>
          <li v-for="sort in sortTitleList" :key="sort.id" @click="srotContent(sort)">
            <span :class="[sort.id ===sortType ?'select3':'']" >{{sort.title}}</span>
            <img :class="[sort.id ===sortType ?'imgRotate':'']" src="../assets/logo.png"/>
          </li>
        </ul>
      </div>
      <div class="video_list" v-if="this.curFeature === 'learnToSing'">
        <ul class="list_main" >
          <li class="fl" v-for="item in contentInfoList" :key="item.url" @click="gotoAddress({name: item.link +`-${curFeature}`, params:{url:item.url}})">
            <div class="list_w" >
              <p style="height: 2rem;"><img :src="item.shortPic" style="max-height: 2rem; width: 3.12rem" ></p>
              <div class="trans">
                  <span> {{item.like}}</span>
                  <span> {{item.icon}}</span>
              </div>
              <p style="position: relative; top: -.3rem; word-wrap: break-word; word-break: break-all" class="fs_26 c_333">{{item.title}}</p>
            </div>
          </li>
          <div class="clear"></div>
        </ul>
      </div>
      <div class="content" style="margin-bottom: .5rem" v-else-if="this.curFeature === 'followToSing'">
        <ul class="follow" style="text-align: left">
          <li class="follow_list mt_10" v-for="item in contentInfoList" :key="item.url">
            <div class="img"  @click="gotoFollowAddress({name: item.link +`-${curFeature}`, params:{url:item.url}})" >
              <img style="border-radius: 5%"  :src="item.shortPic" width="100%">
            </div>
            <div class="text">
              <p class="fs_26 c_333">{{item.title}}</p>
              <p class="fs_26 c_81">{{item.singer}} | {{item.time}}</p>
              <p class="fs_24 c_bbb">{{item.song}}
                <span  @click="gotoAddress({name: item.link +`-${curFeature}`, params:{url:item.url}})" class="follow_a fr"></span>
              </p>
            </div>
            <div class="clear"></div>
          </li>
          <div class="clear"></div>
        </ul>
      </div>
      <div class="video_list" v-if="this.curFeature === 'examination'">
        <ul class="list_main" >
          <li class="fl" v-for="item in contentInfoList" :key="item.url" @click="gotoExaminationAddress({name: item.link +`-${curFeature}`, params:{url:item.url}})">
            <div class="list_w" >
              <p style="height: 2rem;"><img :src="item.shortPic" style="max-height: 2rem; width: 3.12rem" ></p>
              <div class="trans">
                <span> {{item.like}}</span>
                <span> {{item.icon}}</span>
              </div>
              <p style="position: relative; top: -.3rem; word-wrap: break-word; word-break: break-all" class="fs_26 c_333">{{item.title}}</p>
            </div>
          </li>
          <div class="clear"></div>
        </ul>
      </div>
    </section>
    <p v-if="change === true" class="tc" style="line-height: .5rem;" @click="refreshList()"><span class="c_ff6e97 fs_24"><img :src="refreshPic"></span></p>
  </div>
</template>
<style scoped="">
  .content-sort {
     height: .4rem;
     width: 100%;
     background-color: aliceblue;
     text-align: left;
     display: flex;
     justify-content: space-around;
   }
  .content-sort >div {
    height: .4rem;
  }
  .fenlei  img {
    transition: All .5s ease-in-out;
    -webkit-transition: All .5s ease-in-out;
    -moz-transition: All .5s ease-in-out;
    -o-transition: All .5s ease-in-out;
    position: relative;
    width: .2rem;
    height: .25rem;
    top: .05rem;
    display: inline-block;
  }
  .imgRotate {
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
  }
  .list_main li .trans {
    position: relative;
    top: -.4rem;
    width: 100%;
    height: .4rem;
    background: rgba(0,0,0,.5);
  }
  .list_main .trans span {
    float: left;
    width: 50%;
    font-size: .2rem;
    line-height: .4rem;
    color: #ebebea;
  }
</style>
<script>
import refreshPic from '@/assets/images/study-08.png'
import sv from '@/service/service'
//    import fid from '@/service/fid'
import {mapGetters, mapActions} from 'vuex'
import { Indicator } from 'mint-ui'

export default {
  props: {
    mode: {
      type: String,
      default: 'title'
    },
    limit: {
      type: Number,
      default: 10
    },
    page: {
      type: Number,
      default: 1
    },
    filter: {
      type: String,
      default: 'difficulty,tags,fid,keywords,hits,posttime'
    },
    change: {
      type: Boolean,
      default: false
    },
    refresh: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  data () {
    return {
      refreshPic: refreshPic,
      contentInfoList: [
      ],
      getList: [],
      sortType: 'time',
      sortTitleList: [
        {
          title: '',
          id: 'time'
        },
        {
          title: '',
          id: 'rank'
        },
        {
          title: '',
          id: 'recommend'
        }
      ]
    }
  },
  computed: {
    ...mapGetters(
      {
        curFeature: 'currentFeature',
        currentCategory: 'currentCategory',
        currentLevel: 'currentLevel',
        currentFid: 'currentFid',
        searchTags: 'searchTags'
      })
  },
  watch: {
    currentFid: {
      immediate: true,
      handler: async function () {
        console.log('content list vue:currentFid', this.currentFid)
        if (this.curFeature === 'examination') {
          console.log('examination', this.currentCategory)
          await this.getPaperList()
        } else {
          await this.getVideoList()
        }
        this.contentInfoList = this.getList
      }
    },
    currentLevel: async function () {
      console.log('content list vue:currentFid', this.currentLevel)
      await this.getVideoList()
      this.contentInfoList = this.getList
    },
    searchTags: async function (val, oldVal) {
      if (val !== '') {
        await this.getVideoListByTags(val)
        // 
        this.contentInfoList = this.getList
        this.setSearchTags('')
      }
    },
    page: async function () {
      console.log('')
      if (this.curFeature === 'examination') {
        console.log('examination', this.currentCategory)
        await this.getPaperList()
      } else {
        await this.getVideoList()
      }
      this.contentInfoList.concat(this.getList)
    },
    refresh: async function (val, oldVal) {
      if (val) {
        if (this.curFeature === 'examination') {
          console.log('examination', this.currentCategory)
          await this.getPaperList()
        } else {
          await this.getVideoList()
        }
        this.contentInfoList = this.getList
      }
    }
  },
  methods: {
    ...mapActions({setSearchTags: 'setSearchTags', setPaperId: 'setPaperId'}),
    gotoAddress: function (path) {
      this.$router.push(path)
    },
    gotoFollowAddress: function (path) {
      this.$router.push(path)
    },
    gotoExaminationAddress: function (path) {
      console.log('path', path)
      this.setPaperId(path.params.url)
      this.$router.push(path)
    },
    srotContent (sort) {
      this.sortType = sort.id
      this.contentInfoList.reverse()
    },
    cutStr (str, len) {
      let cutStr = ''
      if (str.length > len) {
        cutStr = str.substring(0, len)
        cutStr = cutStr.concat('...')
        return cutStr
      } else {
        return str
      }
    },
    async getVideoListByTags (tags) {
      let params = {
        posttime: 'desc',
        filter: this.filter,
        tags: tags
      }
      //    console.log(params)
      Indicator.open({ text: 'Loading...', spinnerType: 'fading-circle' })
      let svResponse = await sv.getVideoList(params)
      if (svResponse.status === 'ok') {
        this.getList = svResponse.data.map((item) => {
          return {
            title: this.cutStr(item.attributes.title, 20),
            shortPic: item.attributes.cover,
            like: item.attributes.count_marked,
            icon: item.attributes.count_played,
            date: new Date(item.attributes.posttime * 1000).toLocaleString(),
            category: '',
            link: 'detail',
            url: item.id,
            singer: item.singer,
            time: item.time,
            song: item.song
          }
        })
      } else {
        this.$router.push('/login')
      }
      Indicator.close()
    },
    async getVideoList () {
      let params = {
        limit: this.limit,
        page: this.page,
        filter: this.filter,
        fid: this.currentFid,
        difficulty: this.currentLevel
      }
      if (this.mode === 'hits') params.hits = 'desc'
      if (this.mode === 'posttime') params.posttime = 'asc'
      //    console.log(params)
      Indicator.open({ text: 'Loading...', spinnerType: 'fading-circle' })
      let svResponse = await sv.getVideoList(params)
      console.log('svResponse', svResponse)
      if (svResponse.status === 'ok') {
        this.getList = svResponse.data.map((item) => {
          return {
            title: this.cutStr(item.attributes.title, 20),
            shortPic: item.attributes.cover,
            like: item.attributes.count_marked,
            icon: item.attributes.count_played,
            date: new Date(item.attributes.posttime * 1000).toLocaleString(),
            category: '',
            link: 'detail',
            url: item.id,
            singer: item.singer,
            time: item.time,
            song: item.song
          }
        })
      } else {
        this.$router.push('/login')
      }
      Indicator.close()
    },
    async getPaperList () {
      let params = {
        limit: this.limit,
        filter: this.filter,
        category: this.currentFid,
        difficulty: this.currentLevel
      }
      //    console.log(params)
      Indicator.open({ text: 'Loading...', spinnerType: 'fading-circle' })
      let svResponse = await sv.getPaperList(params)
      if (svResponse.status === 'ok') {
        this.getList = svResponse.data.map((item) => {
          return {
            //            title: this.cutStr(item.attributes.title, 20),
            shortPic: item.attributes.cover,
            like: item.attributes.is_marked,
            icon: item.attributes.hits,
            date: new Date(item.attributes.posttime * 1000).toLocaleString(),
            category: '',
            link: 'detail',
            url: item.id,
            singer: item.singer,
            time: item.time,
            song: item.song
          }
        })
      } else {
        this.$router.push('/login')
      }
      Indicator.close()
    },
    refreshList: function () {
      ;
    }
  },
  created: function () {
  },
  mounted: function () {
    //    console.log('currentLevel', this.currentLevel)
    // console.log('curFeature', this.curFeature)
    //    this.fid = fid.getFid(this.curFeature, 'chinese')
    //    console.log('content fid', this.currentFid)
    //    console.log('current feature', this.curFeature)
    //    console.log('current currentLevel', this.currentLevel)
    //    console.log('currentCategory', this.currentCategory)
    //    this.getVideoList()
    // console.log('content list vue:mounted', this.currentFid)
    // console.log('mounted')
    // console.log('examination', this.$route.params)
    //    if (this.curFeature === 'examination') {
    //      this.getPaperList()
    //    } else {
    //      this.getVideoList()
    //    }
  }
}
</script>



