<template>
  <div>
    <header class="header container mt_380">
      <div class="tc ">
        <img :src="chaPhoto" width="130" height="130" class="round">
      </div>
    </header>
    <section class="section container mt_30">
      <div class="msg_int">
        <p ><em class="em01" ></em><input type="text" v-model="formName.user" name="" placeholder="" class="int"></p>
        <p ><em class="em02"></em><input type="password" v-model="formName.password"  name="" placeholder="" class="int"></p>
      </div>
      <div class="submit mt_300">
        <p class="submit_a" @click="doLogin()"></p>
        <p class="lh50 c_999 "><span class="fr"><span @click="register()" class="c_ff6e97"></span></span></p>
        <!--<p class="lh50 c_999 tc "></p>-->
        <!--<p class="lh50 c_999 tc ">-->
          <!--<a href="" class="dis_b w33 fl "><img :src="wechat"></a>-->
          <!--<a href="" class="dis_b w33 fl "><img :src="qq"></a>-->
          <!--<a href="" class="dis_b w33 fl "><img :src="weibo"></a>-->
        <!--</p>-->
      </div>
    </section>
  </div>
</template>
<style scoped="">
</style>
<script>
/* eslint-disable */
import photo from '@/assets/images/head_pic.jpg'
import wechat from '@/assets/images/icon_wechat.png'
import qq from '@/assets/images/icon_qq.png'
import weibo from '@/assets/images/icon_weibo.png'
import { Toast } from 'mint-ui'
import { Indicator } from 'mint-ui'
import sv from '../service/service.js'
import { mapActions} from 'vuex'

export default {
  name: '',
  data () {
    return {
      chaPhoto: photo,
      wechat: wechat,
      qq: qq,
      weibo: weibo,
      mode : 'login',
      pageTitle: '',
      formName: {//
        user: '',
        userState: 'error',
        userError: '',
        password: '',
        passwordError: '',
        passwordState : 'error',
        beDisabled: true,
        registerUrl : '/register'
      },
      passwordChecked: true,
      beShow: false//
    }
  },
  watch:{
    'formName.user' :function(val,oldVal){
      if (this.formName.user === '') {
        this.formName.userError = ''
      }else{
        this.formName.userError = '';
      }
    },
    'formName.password' : function (val,oldVal) {
      if (this.formName.password === '') {
        this.formName.passwordError = ''
      }else{
        this.formName.passwordError = '';
      }
    }
  },
  methods: {
    ...mapActions (
      ['setLoadingState', 'setUserInfo', 'setUserData']
    ),
    resetForm:function(){
      this.formName.user = '';
      this.formName.userError = '';
      this.formName.password = '';
      this.formName.passwordError = '';
    },
    doLogin: async function(){
      var user = this.formName.user,
          password = this.formName.password;
      let data = {
        username: user,
        password: password
      };
      Indicator.open({ spinnerType: 'fading-circle' })

      let svResponse = await sv.login(data)
      debugger
      this.setLoadingState(true);
      if (!svResponse.meta.error) {
        let userInfo = {
          type: svResponse.data.type,
          id: svResponse.data.id
        };
        Indicator.close();
        this.setUserInfo(userInfo);
        this.setUserData(svResponse.data.attributes);
        this.$router.push('/homePage/learnToSing')
      }else{
        Toast({
                message: '',
              });
        Indicator.close()
      }
    },
    inputBlur:function(errorItem,inputContent){
      if (errorItem === 'user') {
        if (inputContent === '') {
          this.formName.userError = ''
        }else{
          this.formName.userError = '';
        }
      }else if(errorItem === 'password') {
        if (inputContent === '') {
          this.formName.passwordError = ''
        }else{
          this.formName.passwordError = '';
        }
      }
      //
      if (this.formName.user != '' && this.formName.password != '') {
        this.formName.beDisabled = false;
      }else{
        this.formName.beDisabled = true;
      }
    },
    remeberPassword:function () {
      console.log(this.formName.password);
    },
    forgetPassword:function () {
      console.log(this.formName.user);
    },
    register:function () {
      console.log('regiseter');
      this.$router.push('/register')
    },
  }
  ,
  mounted() {

  },
  beforeRouteLeave (to, from, next) {
    document.body.removeAttribute('class', 'bg03')
    next()
  }
}
</script>



