<template>
  <div class="video_text " style="text-align: left">
    <h3 class="fs_30 lh50">{{videoInfo.title}}</h3>
    <p class="fs_24 lh35 c_999">{{videoInfo.info}}</p>
    <p class="fs_30 c_ff6e97 lh50">{{videoInfo.score}} 
      <star-rate :disabled="scored" :value="rate" type="star1" active-color="hotpink" inactive-color="#ccc" :star-half="true" hover-color="lightpink" :on-change="postScore"/></p>
  </div>
</template>
<style scoped="">
</style>
<script>
import StarRate from 'vue-cute-rate'
import playIcon from '../assets/sel.png'
import { Toast } from 'mint-ui'

export default {
  props: {
    videoInfo: {
      type: Object,
      default: null
    }
  },
  components: {
    StarRate
  },
  data () {
    return {
      playIcon: playIcon,
      scored: false
    }
  },
  computed: {
    rate: function () {
      return this.videoInfo.score / 2
    }
  },
  methods: {
    postScore (val) {
      console.log('pingfen', val)
      Toast({message: 'O(_)O~', duration: 1000})
      this.videoInfo.score = val * 2
      this.scored = true
    }
  },
  mounted: function () {
  }
}
</script>



