<template>
    <div id="footerNav">
      <mt-tabbar v-model="selected" >
        <mt-tab-item v-for="navigator in navigationList" :key="navigator.title" :id="navigator" >
          <img class="navigator-img" slot="icon" :src="navigator.picRes">
          <span class="navigator-text">{{navigator.title}}</span>
        </mt-tab-item>
      </mt-tabbar>
    </div>
</template>
<style scoped="">
  #footerNav .mint-tabbar {
    height: 1rem;
    /*background-color: hotpink;*/
    color: aliceblue;
    z-index: 1001;
  }
  #footerNav .navigator-img {
    height: .45rem;
    width: .45rem;
    position: relative;
    left:-.1rem;
    top: 0.03rem;
  }

  #footerNav .navigator-text {
    margin-top: .6rem;
    font-size: .22rem;
    position: relative;
    top:.33rem
  }

  #footerNav .mint-tabbar > .mint-tab-item.is-selected {
    background-color: #ffedf8;
    color: #c254ca;
  }
</style>
<script>

import LogoSel from '../assets/sel.png'
import sv from '../service/service'
import {mapActions, mapGetters} from 'vuex'

export default {
  components: {},
  data () {
    return {
      selected: {},
      navigationList: [
        {
          title: '',
          id: 'learnToSing',
          fid: 43,
          picRes: require('../assets/images/video_icon01.png'),
          selectRes: LogoSel,
          link: '/homePage/learnToSing'
        }, {
          title: '',
          id: 'followToSing',
          fid: 44,
          picRes: require('../assets/images/video_icon02.png'),
          selectRes: LogoSel,
          link: '/homePage/followToSing'
        },
        {
          title: '',
          id: 'dictionary',
          fid: 53,
          picRes: require('../assets/images/video_icon05.png'),
          selectRes: LogoSel,
          link: '/homePage/dictionary'
        },
        {
          title: '',
          id: 'examination',
          fid: 98,
          picRes: require('../assets/images/video_icon04.png'),
          selectRes: LogoSel,
          link: '/homePage/examination'
        },
        {
          title: '',
          id: 'profile',
          fid: 99,
          picRes: require('../assets/images/video_icon05.png'),
          selectRes: LogoSel,
          link: '/homePage/profile'
        }
      ]
    }
  },
  computed: {
    ...mapGetters({testDone: 'testDone', curFeature: 'currentFeature', curFid: 'currentFid'})
  },
  watch: {
    curFeature: function (val, oldVal) {
      console.log('curFeature', val)
      let sel = this.navigationList.find((item) => {
        return item.id === val
      })
      // 
      if (this.unwatch) this.unwatch()
      // 
      let tempPic = sel.picRes
      sel.picRes = sel.selectRes
      sel.selectRes = tempPic
      tempPic = this.selected.picRes
      this.selected.picRes = this.selected.selectRes
      this.selected.selectRes = tempPic
      this.selected = sel
      // 
      this.unwatch = this.$watch('selected', this.aChangeHandler)
    }
  },
  methods: {
    ...mapActions({setCurrentFeature: 'setCurrentFeature', setHeaderType: 'setHeaderType'}),
    goto: function (url) {
      this.$router.push(url)
    },
    async getCXTFeatures () {
      let svResponse = await sv.getHomeFeatures()
      if (svResponse.status === 'ok') {
        let navFooterList = svResponse.data.map(
          ({
            type,
            id,
            attributes,
            links
          }) => {
            return {
              title: attributes.title,
              id: id,
              fid: attributes.fid
            }
          }
        )
        console.log('this.navFooterList', navFooterList)
        navFooterList.forEach((i) => {
          let feature = this.navigationList.find((item) => { return item.title === i.title })
          if (typeof feature !== 'undefined') {
            feature.fid = i.fid
          }
        })
      } else {
        this.$router.push('/login')
      }
    },
    aChangeHandler: function (val, oldVal) {
      this.$router.push(val.link)
      //    
      let tempPic = val.picRes
      val.picRes = val.selectRes
      val.selectRes = tempPic
      tempPic = oldVal.picRes
      oldVal.picRes = oldVal.selectRes
      oldVal.selectRes = tempPic
      //    header
      if (val.id === 'rankList') {
        this.setHeaderType('header')
      } else if (val.id === 'examination') {
        this.setHeaderType('search')
      } else {
        this.setHeaderType('')
      }
      if (!this.testDone) {
        return
      }
      //    fid
      this.setCurrentFeature(val)
    }
  },
  mounted: function () {
    this.unwatch = this.$watch('selected', this.aChangeHandler)
    this.setCurrentFeature({id: 'learnToSing', fid: 43})
    this.setHeaderType('')
    //    this.getCXTFeatures()
  }
}
</script>



