<template>
    <div>
      <com-nav-bar></com-nav-bar>
      <com-scroll ref="detailScroll" style="top: 2.38rem;" :pulldown="pulldown" @pulldown="loadData" :pullup="pullup" @scrollToEnd="loadMore" :refreshDelay="300">
        <div class="sort">
          <div class="refreshing" v-show="refreshShow" >...</div>
          <com-content-list  :mode="'sort'" :page="page" :refresh="refreshShow"></com-content-list>
          <div class="loading" v-show="loadShow" >{{pullText}}</div>
        </div>
      </com-scroll>
    </div>
</template>
<style scoped="">
  .sort .refreshing {
    height: 1rem;;
    font-size: .26rem;
    line-height: .7rem;
    font-weight: bold;
    color: #5a5a5a;
    text-align: center;
  }
  .sort .loading {
    width: 100%;
    height: .26rem;
    font-size: .26rem;
    line-height: 1;
    font-weight: bold;
    color: #5a5a5a;
    text-align: center;
    background: rgba(0,0,0,0);
  }
</style>
<script>
import ComNavBar from '@/components/com_nav_tab.vue'
import ComScroll from '@/components/com_scroll.vue'
import ComContentList from '@/components/com_content_list.vue'
import {mapGetters, mapActions} from 'vuex'

export default {
  props: {
  },
  components: {
    ComNavBar, ComScroll, ComContentList
  },
  data () {
    return {
      page: 1,
      refresh: false,
      category: '',
      refreshShow: false,
      loadShow: false,
      pulldown: true,
      pullup: true,
      pullText: ''
    }
  },
  computed: {
    ...mapGetters({curFeature: 'currentFeature', curCategory: 'currentCategory', curLevel: 'currentLevel', curFid: 'currentFid', curHeader: 'headerType'})
  },
  methods: {
    ...mapActions(
      {
        setHeaderType: 'setHeaderType',
        addPreState: 'addPreState',
        delPreState: 'delPreState'
      }
    ),
    loadData () {
      this.refreshShow = true
      setTimeout(() => {
        this.refreshShow = false
      }, 1000)
    },
    loadMore () {
      this.loadShow = true
      let moreComment = 5
      if (moreComment.length === 0) {
        this.pullText = ''
      } else {
        this.pullText = '...'
      }
      this.page += 1
      setTimeout(() => {
        if (moreComment.length === 0) {
          this.pullText = ''
        } else {
          this.pullText = ''
        }
        this.loadShow = false
      }, 2000)
      moreComment = 0
    }
  },
  mounted: function () {
    let category = this.$route.params
    this.category = category.category
    this.setHeaderType('search')
  },
  beforeRouteLeave (to, from, next) {
    let preStare = {
      preFeature: this.curFeature,
      preFid: this.curFid,
      preLevel: this.curLevel,
      preHeader: this.curHeader,
      preCategory: this.curCategory
    }
    this.addPreState(preStare)
    next()
  }
}
</script>



