<template>
  <div class="search_box">
    <div class="search_main">
      <div class="return_a fl ">
        <a @click="goBack"></a>
      </div>
      <div class=" tc fs_38">{{title}}</div>
      <div v-if="buttonShow" class="search_btn fr" style="position: absolute;right: .2rem;">{{buttonCon}}</div>
    </div>
    <div class="clear"></div>
  </div>
</template>
<style>

</style>
<script>

import {mapGetters, mapActions} from 'vuex'
export default {
  props: {
    title: {
      type: String,
      default: ''
    },
    buttonShow: {
      type: Boolean,
      default: false
    },
    buttonCon: {
      type: String,
      default: ''
    }
  },
  components: {},
  computed: {
    ...mapGetters({preState: 'preState'})
  },
  data () {
    return {}
  },
  methods: {
    ...mapActions(
      {
        delPreState: 'delPreState'
      }),
    goBack () {
      let changeState = this.preState[ this.preState.length - 1 ]
      //      console.log('changeState', changeState)
      //      //      this.setCurrentFeature(changeState.preFeature)
      //      //      this.setCurrentLevel(changeState.preLevel)
      //      //      this.setCurrentCategory(changeState.preCategory)
      //      //      this.setCurrentFid(changeState.preFid)
      //      //      this.setHeaderType(changeState.preHeader)
      this.delPreState(changeState)
      this.$router.back()
    }
  },
  mounted: function () {
  }
}
</script>



