<template>
  <div>
    <header class="header study  bg04">
      <div class="search_box">
        <div class="search_main">
          <div class="return_a fl ">
            <a @click="goBack"></a>
          </div>
        </div>
        <div class="clear"></div>
      </div>
      <div class="me_nav">
        <div class="me_img tc" @click="gotoAddress({name: 'system-changeIcon', params:{url:'changeicon'}})">
          <p class="b_r" style="height: 1.35rem">
            <img :src="curUser.icon">
          </p>
          <p class="fs_26 lh45">{{curUser.nick}}</p>
        </div>
        <ul class="me_navlist">
          <li v-for="item in myProfileList" v-bind:key="item.address"
              @click="gotoAddress({name: 'detail-profile', params:{url:item.address}})">
            <p class=""><img :src="item.picRes"></p>
            <p class="lh70 c_999">{{item.name}}</p>
          </li>
          <div class="clear"></div>
        </ul>
      </div>
    </header>
    <h3 class="fs_36 set" style="margin: .3rem;float: left;"></h3>
    <com-scroll style="top: 5.5rem; bottom: 1rem">
      <div>
        <section class="section  study container" style="padding-bottom: 1rem; text-align: left">
          <ul class="set_list">
            <li>
              <a>
                <em class="me_icon me-23"></em> 
                <em class="em_right fr"></em>
                <span @click="checkOut" class="on-off"></span>
              </a>
            </li>
            <li @click="gotoAddress({name: 'system-password'})">
              <a>
                <em class="me_icon me-34"></em> 
                <em class="em_right fr"></em>
              </a>
            </li>
            <li @click="gotoAddress({name: 'system-aboutUs'})">
              <a>
                <em class="me_icon me-19"></em> 
                <em class="em_right fr"></em>
              </a>
            </li>
            <li @click="gotoAddress({name: 'system-suggestion'})">
              <a>
                <em class="me_icon me-38"></em> 
                <em class="em_right fr"></em>
              </a>
            </li>
            <li>
              <a href="javascript:">
                <em class="me_icon me-30"></em> 
                <span class="fr c_999">2.1.1</span>
              </a>
            </li>
            <li @click="gotoAddress({name: 'system-invitation'})">
              <a>
                <em class="me_icon me-42"></em> 
                <em class="em_right fr"></em>
              </a>
            </li>
            <div class="clear"></div>
          </ul>
          <div class="submit mt_30" style="margin-top: .5rem">
            <p><a @click="doLogout" class="submit_a"></a></p>
          </div>
        </section>
      </div>
    </com-scroll>
  </div>
</template>
<style>
  .set_list li {
    display: flex;
  }
</style>
<script>/*eslint-disable*/

import ComScroll from '../../components/com_scroll.vue'
import ComReturn from '../../components/com_return.vue'
import songlist from '../../assets/images/me.png'
import followList from '../../assets/images/me-06.png'
import dictionary from '../../assets/images/me-09.png'
import questionList from '../../assets/images/me-12.png'
import {mapGetters, mapActions} from 'vuex'
import {Indicator} from 'mint-ui'
import sv from '../../service/service.js'

export default {
  components: {
    ComScroll, ComReturn
  },
  data() {
    return {
      myProfileList: [
        {
          picRes: songlist,
          name: '',
          address: 'songList'
        },
        {
          picRes: followList,
          name: '',
          address: 'followList'
        },
        {
          picRes: dictionary,
          name: '',
          address: 'dictionary'
        },
        {
          picRes: questionList,
          name: '',
          address: 'questionList'
        }
      ],
      systemFeature: [
        {}
      ]
    };
  },
  computed: {
    ...mapGetters(
      {
        curUser: 'currentUser',
        curFeature: 'currentFeature',
        curCategory: 'currentCategory',
        curLevel: 'currentLevel',
        curFid: 'currentFid',
        curHeader: 'headerType',
        preState: 'preState'
      })
  },
  methods: {
    checkOut: function () {
      console.log('check out');
    },
    gotoAddress: function (path) {
      this.$router.push(path);
    },
    goBack: function () {
      let changeState = this.preState[this.preState.length - 1];
      this.delPreState(changeState);
      this.$router.back();
    },
    async srvGetUserProfile() {
      let userInfo = await sv.getUserInfo();
      this.setCurUser(userInfo);
      console.log(this.curUser);
    },
    doLogout: async function () {
      Indicator.open({spinnerType: 'fading-circle'})
      let svResponse = await sv.logout()
      if (svResponse.status === 'ok') {
        this.$store.dispatch('setSignOut')
        Indicator.close()
        this.$router.push('/login')
      } else {
        Indicator.close()
      }
    },
    ...mapActions({setCurUser: 'setCurUser', addPreState: 'addPreState', delPreState: 'delPreState'}),
  },
  mounted: async function () {
    // ,  state
    await this.srvGetUserProfile();
  },
  beforeRouteLeave(to, from, next) {
    let preStare = {
      preFeature: this.curFeature,
      preFid: this.curFid,
      preLevel: this.curLevel,
      preHeader: this.curHeader,
      preCategory: this.curCategory
    };
    this.addPreState(preStare);
    next();
  }
};
</script>



