<template>
    <div>
      <com-vedio></com-vedio>
      <com-scroll ref="videoScroll" style=" top: 7.3rem"  :refreshDelay="300">
        <div>
          <section class="section  video container" style="padding-bottom: 2rem; ">
            <video-relative></video-relative>
          </section>
        </div>
      </com-scroll>
    </div>
</template>
<style>

</style>
<script>
import ComVedio from '@/components/com_video_player.vue'
import VideoInfo from '@/components/com_video_info.vue'
import VideoRelative from '@/components/com_relative_content.vue'
import ComScroll from '@/components/com_scroll.vue'
import {mapActions} from 'vuex'

export default {
  components: {
    ComVedio, VideoInfo, VideoRelative, ComScroll
  },
  data () {
    return {
      maId: 'xxx'
    }
  },
  methods: {
    ...mapActions(
      {
        setHeaderType: 'setHeaderType'
      }
    )
  },
  mounted: function () {
    console.log('this.$reouter', this.$route.params)
    this.setHeaderType('')
    this.maId = this.$route.params.url
  }
}
</script>



