import Vue from 'vue'
import Vuex from 'vuex'
import createLogger from 'vuex/dist/logger'
import common from './module/common'
import user from './module/user'

Vue.use(Vuex)
const store = new Vuex.Store(
  {
    modules: {
      common, user
    },
    plugins: [createLogger()]
  }
)

export default store



