/*eslint-disable*/

let audio = {
  _au          : null,
  _isPlaying   : false,
  _play        : function (url) {
    if (!this._isPlaying) {
      this._isPlaying = true;
      setTimeout(() => {
        let au = this._au = document.createElement('audio');
        au.onended  = () => {
          this._isPlaying = false;
        };
        au.src      = url;
        au.autoplay = 'autoplay';
      }, 0);
    }
  },
  stop         : function () {
    this._au.muted = true;
  },
  play         : function (url) {
    this._play(url);
  }
};

export default audio;



