<template>
    <div class="play_bg">
      <header class="header container" style="height: 0.9rem">
        <div class="return_a2 fl ">
          <a href=""></a>
        </div>
        <div class="return_a3 fr" style="line-height: .72rem">
          <a href="" ><img src="../../../assets/images/icon_share.png"></a>
          <a href="" style="margin-right: 0.3rem"><img src="../../../assets/images/icon_star2.png"></a>
        </div>
        <div class="clear"></div>
      </header>
      <section class=" ">
       <com-audio ref="audioPlayer"></com-audio>
      </section>
      <footer class="footer" style="z-index: 999999;">
        <ul class="play_bar">
          <li @click="restart">
              <p><img src="../../../assets/images/icon_again.png"></p>
              <p class="bar_text"></p>
          </li>
          <li @click="pauseOrStart">
              <p><img src="../../../assets/images/icon_pause.png"></p>
              <p class="bar_text"></p>
          </li>
          <li>
              <p><img src="../../../assets/images/icon_finish.png"></p>
              <p class="bar_text"></p>
          </li>
          <li>
              <p><img src="../../../assets/images/icon_original.png"></p>
              <p class="bar_text"></p>
          </li>
        </ul>
      </footer>
    </div>
</template>
<style>

</style>
<script>
import comAudio from '@/components/com_audio_player.vue'
import {mapActions} from 'vuex'
export default {
  components: {
    comAudio
  },
  data () {
    return {
      rangeValue: 0,
      audioURL: ''
    }
  },
  methods: {
    ...mapActions({setCurrentFeature: 'setCurrentFeature', setHeaderType: 'setHeaderType'}),
    pauseOrStart: function () {
      this.$refs.audioPlayer.pausePlayer()
    },
    restart: function () {
      this.$refs.audioPlayer.restart()
    }
  },
  mounted: function () {
    this.setHeaderType('')
    console.log(this)
  }
}
</script>



