<template>
    <div>
      <com-swiper></com-swiper>
      <div class="no-rank" v-if="mySongRank === false">
        <div class="songName">
          
        </div>
        <div class="tosing"><em class="em_mi"></em><span style="color: hotpink; font-weight: bold;margin: 0 .2rem"> </span></div>
      </div>
      <div class="my_rank" style="margin-top: .2rem" v-else>
        <div class="w1"></div>
        <div class="w2"><img src="../../assets/images/head_img.jpg" ></div>
        <div class="w3">
          <p class="name">Mini</p>
          <p class="fs_28 c_ff6e97">11</p>
        </div>
        <div class="w4"><em class="em_mi"></em> </div>
        <div class="w5"><span class="c_ff6e97 fs_28">95.5</span></div>
        <div class="w6"><em class="em_collect"></em></div>
        <div class="clear"></div>
      </div>
      <com-scroll ref="scroll" style="top: 7.7rem;" :refreshDelay="300" >
        <div>
          <com-rank-list></com-rank-list>
       </div>
      </com-scroll>
    </div>
</template>
<style scoped="">
  .no-rank {
   height: 1.8rem;
  }
  .songName {
     width: 100%;
     float: right;
     height: .8rem;
     line-height: .8rem;
     font-size: .3rem
   }
   .tosing {
    position: relative;
    width: 100%;
    line-height: .8rem;
    font-size: .3rem;
  }
</style>
<script>
import ComScroll from '../../components/com_scroll.vue'
import ComRankList from '@/components/com_rank_list.vue'
import ComSwiper from '../../components/com_swiper.vue'
import {mapActions} from 'vuex'
import profileImg from '@/assets/myprofile.jpg'

export default {
  components: {
    ComRankList, ComSwiper, ComScroll
  },
  data () {
    return {
      myProfile: {
        avatar: profileImg,
        nick: '',
        song: '',
        points: '80',
        rank: '15'
      }
    }
  },
  computed: {
    mySongRank: function () {
      if (this.myProfile === null || !this.myProfile) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    ...mapActions(
      {
        setHeaderContent: 'setHeaderContent',
        setHeaderType: 'setHeaderType'
      }
    )
  },
  mounted: function () {
    this.setHeaderType('header')
    this.setHeaderContent('')
  }
}
</script>



