<template>
  <div class="bg01" style="height: 100% ;text-align: left">
    <header class="header container">
      <div class="return_a">
        <a @click="gotoLogin()"></a>
      </div>
      <div class="header_icon tc">
        <img :src="headpic" width="60" height="60">
      </div>
    </header>
    <section class="section container">
      <div class="msg_int">
        <p><em class="em01"></em><input type="text" v-model="formName.user"  placeholder="" class="int"></p>
        <!--<p><em class="em02"></em><input type="text" v-model="formName.password" placeholder="" class="int"></p>-->
        <p><em class="em03"></em><input type="text" v-model="formName.IdentifyingCode" placeholder="" class="int"><span @click="getIdentifyCode(formName.user)" class="send"></span></p>
      </div>
      <div class="submit mt_300">
        <p><a @click="doLogin" class="submit_a"></a></p>
        <p class="lh50 c_999 pl_20 mt_10"><span @click="viewTerms()"><img :src="selpic" width="30" height="30"></span> </p>
        <p class="lh50 c_999 pl_20">,<span @click="gotoLogin()" class="c_ff6e97"></span></p>
      </div>
    </section>
  </div>
</template>
<style scoped="">
</style>
<script>
/* eslint-disable */
import headPic from '@/assets/images/icon_header.png'
import selPic from '@/assets/images/icon_select.png'
import { Toast } from 'mint-ui'
import sv from '../service/service.js'
import { Indicator } from 'mint-ui'
import { mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        selpic: selPic,
        headpic: headPic,
        formName: {//
          user: '',
          userError: '',
          password: '',
          passwordError: '',
          confirmPassword:'',
          confirmPasswordError:'',
          IdentifyingCode:'',
          IdentifyingCodeError:'',
          beDisabled: true
        },
        agreementChecked:false,
        beShow: false//
      }
    },
    watch:{
      'formName.user' : function () {
        ;
      },
      'formName.password' : function () {
        ;
      },
      'formName.confirmPassword' : function () {
        ;
      },
    },
    methods: {
      ...mapActions (
        ['setLoadingState', 'setUserInfo', 'setUserData']
      ),
      resetForm:function(){
        this.formName.user = '';
        this.formName.userError = '';
        this.formName.password = '';
        this.formName.passwordError = '';
      },
      submitForm:function(formName){
        //
        //this.$emit('showState', [this.beShow,this.formName.user])
        //user password
        var user = this.formName.user,
            password = this.formName.password;
        console.log(user,password)
        //      Axios.get('../../src/php/login.php?user='+user+'&password='+password)
        //           .then(function(res){
        //             console.log(res)
        //
        //           })
        //           .catch(function(){
        //
        //           })
      },
      inputBlur:function(errorItem,inputContent){
        if (errorItem === 'user') {
          if (inputContent === '') {
            this.formName.userError = ''
          }else{
            this.formName.userError = '';
          }
        }else if(errorItem === 'password') {
          if (inputContent === '') {
            this.formName.passwordError = ''
          }else{
            this.formName.passwordError = '';
          }
        }else if(errorItem === 'consfirmPassword'){
          if(inputContent !== this.formName.password){
            this.formName.confirmPasswordError = ''
          }
        }
        //
        if (this.formName.user != '' && this.formName.password != '') {
          this.formName.beDisabled = false;
        }else{
          this.formName.beDisabled = true;
        }
      },
      checkAgrement:function () {
        console.log(this.formName.password);
      },
      getIdentifyCode: async function (telNum) {
        if (telNum === ''){
          Toast({
                  message: '',
                });
          return
        } else {
          let loginphone = this.formName.user
          let data = {
            loginphone: loginphone
          };
          let svResponse = await sv.login(data)
          if (svResponse.status !== 'ok') {
            Toast({
                    message: '',
                  });
          }else{
            Toast({
                    message: `${loginphone}`,
                  });
          }
        }

      },
      goToLogin : function () {
        this.$router.back();
      },
      viewTerms: function () {
        ;
      },
      doLogin: async function(){
        var loginphone = this.formName.user,
            verifycode = this.formName.IdentifyingCode;
        console.log(loginphone,verifycode)

        let data = {
          loginphone: loginphone,
          verifycode: verifycode
        };

        Indicator.open({ spinnerType: 'fading-circle' })
        this.setLoadingState(true);
        let svResponse = await sv.login(data)
        console.log('svResponse',svResponse)
        if (svResponse.meta.login_check_result === false) {
          Toast({
                  message: '',
                });
          //this.$router.back();
          Indicator.close()
        }else{
          let userInfo = {
            type: svResponse.data.type,
            id: svResponse.data.id
          };
          Indicator.close();
          this.setUserInfo(userInfo);
          this.setUserData(svResponse.data.attributes);
//          localStorage.setItem("token", svResponse.meta.token)
          this.$router.push('/homePage/learnToSing')
        }

      },
      gotoLogin: function () {
        this.$router.push('/login');
      }
    }
  }
</script>



