<template>
    <div>
        <com-scroll ref="scroll" style="top: .7rem; background-color: #ffffff; z-index: 1002; bottom: 0" :refreshDelay="300" >
          <div>
            <div class="back" ref="back"
                 @touchstart.prevent="touchStart"
                 @touchmove.prevent="touchMove"
                 @touchend="touchEnd" >
              <transition :name="transitionMode" mode="out-in">
                  <div class="back-l" v-if=" (currentSubject >= 0) && ( paperContent.attributes.questions.length > currentSubject) && showContent" :key="1">
                    <div class="subject-img">
                      <img v-if="type === 'img'" :src="paperContent.attributes.questions[currentSubject].cover" >
                      <com-vedio v-else></com-vedio>
                      <span class="num">{{currentSubject +1 }}/{{paperContent.attributes.questions.length}}</span>
                    </div>
                    <div class="subject-face">
                      <img v-if="testCheck && selAnswer.toLowerCase() !== paperContent.attributes.questions[currentSubject].answer"
                           :src="wrongAnswer" class="subject-option-image">
                      <span>{{currentSubject +1 }}
                        <!---->
                        <p style="display: inline"  v-html="paperContent.attributes.questions[currentSubject].face"></p>
                      </span>
                      <div v-for="(optionItem, index) in paperContent.attributes.questions[currentSubject].option" v-bind:key="optionItem">
                        <!---->
                        <img v-if="testCheck && paperContent.attributes.questions[currentSubject].answer === optionCell[index].toLowerCase()"
                             :src="rightAnswer" class="subject-option-image">
                        <!---->
                        <span class="subject-option-text" @click="selectItem(index)"  v-if="paperContent.attributes.questions[currentSubject].type === '2'">
                          <span class="unsel" :class="[selAnswer === optionCell[index] ? 'sel' :'']">
                            {{optionCell[index]}}
                          </span>
                          {{optionItem}}
                        </span>
                        <!---->
                        <span class="subject-option-text" @click="multiSelectItem(index)"  v-else>
                           <img v-if="testCheck && paperContent.attributes.questions[currentSubject].answer.indexOf(optionCell[index].toLowerCase()) !== -1"
                                :src="rightAnswer" class="subject-option-image">
                          <span class="unsel" :class="[(typeof selAnswer !== 'undefined') && (selAnswer.indexOf(optionCell[index]) !== -1) ? 'sel' :'']">
                            {{optionCell[index]}}
                          </span>
                          {{optionItem}}
                        </span>
                      </div>
                    </div>
                    <div class="answerButton"  @touchstart="setChooseLayerStatus(true)">{{buttonContent}}</div>
                    <!---->
                    <transition name="fade"  mode="out-in">
                      <div class="answerButton" id="submitTest" v-if="!paperContent.attributes.user_answer.includes(undefined) && !testCheck" @touchstart="patchExamination"></div>
                    </transition>
                    <!---->
                    <div class="explain-content" v-if="testCheck">
                      <div class="explain"></div>
                      <div style="margin: .2rem 0">AAAA,AAAA,AAAA</div>
                      <div style="display: flex ;margin: .2rem 0">
                        <div class="checkPoint" >A</div>
                        <div class="checkPoint" >B</div>
                      </div>
                    </div>
                  </div>
              </transition>
            </div>
          </div>
          <!---->
          <transition  name="fade">
            <com-answer v-if="answerShow" :testStatus="testCheck" :score="score" :wrongNum="wrongNum" :completeTime="completeTime"></com-answer>
          </transition>
        </com-scroll>
      <!---->
        <transition name="slide-fade">
         <com-choose-subject v-if="chooseShow" :userAnswers="paperContent.attributes.user_answer"
                             :rightAnswers="paperContent.attributes.questions" :testDone="testCheck"
                             :wrongNum="wrongNum"></com-choose-subject>
      </transition>
    </div>
</template>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .back
    width: 100%
    height: 100%
    font-size : .3rem
    .back-l
      position: relative
      vertical-align: top
      display: inline-block
      width: 100%
      height: 100%
      background-color: transparent
  .checkPoint
    background-color transparent
    border solid 1px #ff5dcf
    border-radius 2rem
    height .5rem
    font-size .3rem
    line-height .5rem
    padding 0 .3rem
    color  #ff5dcf
    margin 0 .1rem
  .answerButton
    font-size: .4rem
    font-weight: bold
    background-color #ff5dcf
    border-radius 2rem
    color #fff
    width 2.5rem
    height .7rem
    line-height .7rem
    margin .1rem auto
  .subject-img
    img
      width 100%
      height 5rem
  .num
    display: inline-block
    padding: .1rem .35rem
    border-radius: 30px
    background-color: rgba(0, 0, 0, 0.3)
    position: relative
    bottom: 0.2rem
    font-size: 0.24rem
    font-weight: bold
    color: #fff
  .subject-face
    text-align left
    padding 0 .5rem
    div
      font .3rem bold
      word-wrap break-word
      word-break break-all
      padding .1rem 0
    .subject-option-image
      width .32rem
      height .32rem
      vertical-align middle
    .subject-option-text
      line-height: .3rem
      .sel
       background-color hotpink
       color #ffffff
      .unsel
       width 0.4rem
       height .4rem
       text-align center
       line-height .4rem
       border-radius 50%
       display inline-block
  .explain
    border-left: .1rem  solid  #ff5dcf
    font-size: .4rem
    font-weight: bold
    padding-left: .1rem
  .explain-content
    text-align left
    margin 0 .5rem
</style>
<script>
import right from '../../../assets/images/right.png'
import wrong from '../../../assets/images/x.png'
import ComVedio from '@/components/com_video_player.vue'
import ComScroll from '@/components/com_scroll.vue'
import ComAnswer from '@/components/com_answer.vue'
import ComSwiper from '@/components/com_swiper.vue'
import ComChooseSubject from '@/components/com_choose_subject.vue'
import {mapGetters, mapActions} from 'vuex'
import sv from '@/service/service'
import { Toast, MessageBox, Indicator } from 'mint-ui'

export default {
  components: {
    ComVedio, ComScroll, ComAnswer, ComSwiper, ComChooseSubject
  },
  data () {
    return {
      optionCell: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J', 'K', 'L'],
      showContent: true,
      transitionMode: '',
      testCheck: false,
      selAnswer: '',
      percent: 0,
      rightAnswer: right,
      wrongAnswer: wrong,
      type: 'img',
      answer: true,
      paperContent: {
        type: '',
        id: '',
        attributes: {
          id: '',
          test_id: '',
          user_id: '',
          paper_id: '',
          score: '',
          created_at: '',
          updated_at: '',
          started_at: null,
          complated_at: null,
          is_complated: 0,
          questions: [],
          user_answer: []
        }
      },
      completeTime: 0,
      wrongNum: 0,
      score: 0,
      testResponse: {},
      currentSubject: 0,
      buttonContent: '',
      testStatus: false
    }
  },
  computed: {
    ...mapGetters(
      {
        answerShow: 'layerShow',
        chooseShow: 'chooseShow',
        curPaperId: 'paperId',
        curSubjectId: 'currentSubject',
        testDone: 'testDone'
      }
    )
  },
  watch: {
    curSubjectId: function (val, oldVal) {
      if (val > this.currentSubject) {
        this.transitionMode = 'slide-fade-right'
        this.showContent = false
      } else if (val < this.currentSubject) {
        this.transitionMode = 'slide-fade-left'
        this.showContent = false
      }
      this.selAnswer = this.paperContent.attributes.user_answer[val]
      setTimeout(() => {
        this.showContent = true
        this.currentSubject = val
      }, 0)
    }
  },
  created () {
    this.touch = {}
  },
  methods: {
    ...mapActions(
      {
        setLayerStatus: 'setLayerStatus',
        setChooseLayerStatus: 'setChooseLayerStatus',
        setHeaderContent: 'setHeaderContent',
        setHeaderType: 'setHeaderType',
        setTestStatus: 'setTestStatus'
      }
    ),
    async patchExamination () {
      let loading = document.getElementsByClassName('mint-indicator-wrapper')[0]
      loading.style.zIndex = 10000
      Indicator.open({ text: '...', spinnerType: 'fading-circle' })
      let params = {
        path: this.paperContent.attributes.test_id,
        body: {
          data: []
        }
      }
      for (let i = 0; i < this.paperContent.attributes.questions.length; i++) {
        params.body.data.push(
          {
            question_id: this.paperContent.attributes.questions[i].question_id,
            user_answer: this.paperContent.attributes.user_answer[i].toLowerCase()
          })
      }
      //   
      this.testResponse = await sv.patchPaperContent(params)
      //   
      this.setTestStatus(true)
      //   
      await this.calExaminationResult(this.testResponse.data.attributes)
      //   
      this.setLayerStatus(true)
      //   button
      this.buttonContent = ''
      document.getElementById('submitTest').hidden = true
      this.testCheck = true
      Indicator.close()
      loading.style.zIndex = 'auto'
    },
    getCompleteTime (time) {
      let completeMintite = Math.floor((time.complated_at - time.created_at) / 60)
      let completeSecond = (time.complated_at - time.created_at) % 60
      this.completeTime = completeMintite + '' + completeSecond + ''
      console.log('this.completeTime', this.completeTime)
    },
    async calExaminationResult (testResponse) {
      console.log('testResponse', testResponse)
      this.wrongNum = 0
      this.getCompleteTime(testResponse)
      let totalNum = testResponse.user_answer.length
      testResponse.user_answer.forEach((iQuestion) => {
        if (iQuestion.question_answer !== iQuestion.user_answer) {
          this.wrongNum++
        }
      })
      this.paperContent.score = Math.round((1 - (this.wrongNum / totalNum)) * 1000) / 10
      this.score = this.paperContent.score
    },
    multiSelectItem (index) {
      if (typeof this.selAnswer === 'undefined') {
        this.selAnswer = this.optionCell[index]
      } else
      if (this.selAnswer.indexOf(this.optionCell[index]) === -1) {
        this.selAnswer += this.optionCell[index]
      } else {
        this.selAnswer = this.selAnswer.replace(this.optionCell[index], '')
      }
      //  
      this.paperContent.attributes.user_answer[this.currentSubject] = this.selAnswer
    },
    selectItem (index) {
      this.selAnswer = this.optionCell[index]
      this.paperContent.attributes.user_answer[this.currentSubject] = this.optionCell[index]
    },
    touchStart (e) {
      const touch = e.touches[0]
      this.touch.startX = touch.pageX
      this.touch.startY = touch.pageY
    },
    touchMove (e) {
      const touch = e.touches[0]
      const deltaX = touch.pageX - this.touch.startX
      const deltaY = touch.pageY - this.touch.startY
      if (Math.abs(deltaY) > Math.abs(deltaX)) {
        return
      }
      //      console.log('deltaX:', deltaX)
      this.percent = deltaX / window.innerWidth
    },
    touchEnd () {
      if (Math.abs(this.percent) > 0.5) {
        if (this.percent > 0) {
          if (this.currentSubject === 0) {
            Toast({message: '', duration: 500})
            return
          }
          this.transitionMode = 'slide-fade-left'
          console.log('pre')
          this.showContent = false
          setTimeout(() => {
            this.showContent = true
            this.currentSubject = this.currentSubject - 1
            this.selAnswer = this.paperContent.attributes.user_answer[this.currentSubject]
          }, 0)
        } else if (this.percent < 0) {
          if (this.currentSubject === this.paperContent.attributes.questions.length - 1) {
            Toast({message: '', duration: 500})
            return
          }
          console.log('next')
          this.transitionMode = 'slide-fade-right'
          this.showContent = false
          setTimeout(() => {
            this.showContent = true
            this.currentSubject = this.currentSubject + 1
            this.selAnswer = this.paperContent.attributes.user_answer[this.currentSubject]
          }, 0)
        }
      }
      this.percent = 0
    },
    async getPaperContent () {
      // 
      let svResponse = await sv.getPaperTest(this.curPaperId)
      if (svResponse.status === 'ok') {
        svResponse.data.attributes.questions.forEach((item) => {
          if (item.option !== '') {
            item.option = item.option.split('\r\n')
          }
        })
        svResponse.data.attributes.user_answer = new Array(svResponse.data.attributes.questions.length)
        this.paperContent = svResponse.data
        console.log('this.paperContent', this.paperContent)
      }
    }
  },
  mounted: function () {
    this.setHeaderType('header')
    this.getPaperContent()
    this.setHeaderContent('')
  },
  beforeRouteLeave (to, from, next) {
    // 
    //  `this`
    if (!this.testDone) {
      MessageBox.confirm('?').then(action => {
        this.setTestStatus(true)
        this.setChooseLayerStatus(true)
        next()
        this.setLayerStatus(true)
      })
    } else {
      next()
      this.setLayerStatus(true)
    }
  }
}
</script>



