<template>
    <div>
      <header class="header container  bg_fff" >
        <div class="search_box " style="padding-top: .2rem">
          <com-return :title="title" :buttonShow="buttonShow" ></com-return>
        </div>
        <div class="clear"></div>
      </header>
      <com-scroll style="top: 1rem;bottom: 1rem">
        <div>
          <section  style="padding-bottom: 0.88rem " class="bg_fff mt_15">
            <div class="song_list">
                <transition-group name="list-complete" tag="ul"  v-if="urlParame === 'songList'">
                  <li v-for="(item, index) in songList" v-bind:key="item.id" class="list-complete-item">
                    <div class="w1"><img :src="item.picRes" width="100%"></div>
                    <div class="w2">
                      <p class="fs_26">{{item.title}}</p>
                      <p class="fs_24 c_bbb">{{item.songTime}}</p>
                    </div>
                    <div class="w3"></div>
                    <div class="w4" @click="deleteSong(index)"><em class="em_delete"></em></div>
                  </li>
                </transition-group>
              <transition-group name="list-complete" tag="ul"  v-else-if="urlParame === 'followList'">
                  <li v-for="(item, index) in songList" v-bind:key="item.id" class="list-complete-item">
                    <div class="w1"><img :src="item.picRes" width="100%"></div>
                    <div class="w2">
                      <p class="fs_26">{{item.id}}{{item.title}}</p>
                      <p class="fs_24 c_bbb">{{item.songTime}}</p>
                    </div>
                    <div class="w3"><span class="c_ff6e97 fs_28">95.5</span> </div>
                    <div class="w4" @click="deleteSong(index)" ><em class="em_delete"></em></div>
                    <div class="clear"></div>
                  </li>
              </transition-group>
              <div class="clear"></div>
            </div>
            <div class="ranking_list ">
              <transition-group name="list-complete" tag="ul"  v-if="urlParame === 'dictionary'">
                <li v-for="(item, index) in songList" v-bind:key="item.id" class="list-complete-item">
                  <div class="w12">clan</div>
                  <div class="w345">n. ; ; ; n. (Clan); ()</div>
                  <div class="w6" @click="deleteSong(index)" ><em class="em_delete"></em></div>
                  <div class="clear"></div>
                </li>
              </transition-group>
              <div class="clear"></div>
            </div>
            <div class=" subject_list">
              <transition-group name="list-complete" tag="ul"  v-if="urlParame === 'questionList'">
                <li v-for="(item, index) in songList" v-bind:key="item.id" class="list-complete-item">
                  <div class="w1">
                    <p><span class="c_ff6e97 on">91.5</span> They were __ to work in the circus. One elephants name was Oliver.</p>
                  </div>
                  <div class="w2" @click="deleteSong(index)"><em class="em_delete"></em></div>
                </li>
                <div class="clear"></div>
              </transition-group>
              <div class="clear"></div>
            </div>
          </section>
        </div>
      </com-scroll>
    </div>
</template>
<style>

</style>
<script>
import ComScroll from '@/components/com_scroll.vue'
import ComReturn from '@/components/com_return.vue'
import cover from '../../../assets/images/study-46.png'
import sv from '@/service/service'
import { Indicator } from 'mint-ui'
export default {
  components: {
    ComScroll, ComReturn
  },
  data () {
    return {
      title: '',
      buttonShow: true,
      urlParame: '',
      songList: [
        {
          picRes: cover,
          title: '',
          songTime: '2018-04-15 19:03',
          id: 1,
          score: 95
        }
      ],
      followList: [],
      dictionary: [],
      qusetionList: []
    }
  },
  methods: {
    deleteSong (id) {
      this.songList.splice(id, 1)
    },
    cutStr (str, len) {
      let cutStr = ''
      if (str.length > len) {
        cutStr = str.substring(0, len)
        cutStr = cutStr.concat('...')
        return cutStr
      } else {
        return str
      }
    },
    async getMyFollowList (fid) {
      let params = {
        fid: fid
      }
      //    console.log(params)
      Indicator.open({ text: 'Loading...', spinnerType: 'fading-circle' })
      let svResponse = await sv.getMyMarkedList(params)
      console.log('getMyLearnToSing', svResponse)
      if (svResponse.length > 0) {
        this.songList = svResponse.map((item) => {
          let unixTimestamp = new Date(item.attributes.posttime * 1000)
          return {
            title: this.cutStr(item.attributes.title, 20),
            picRes: item.attributes.cover,
            songTime: unixTimestamp.toLocaleString(),
            id: item.id,
            score: 95
          }
        })
        console.log(this.songList)
      } else {
      }
      Indicator.close()
    }
  },
  mounted: function () {
    this.urlParame = this.$route.params.url
    switch (this.urlParame) {
      case 'songList': {
        this.title = ''
        this.getMyFollowList(43)
        break
      }
      case 'followList': {
        this.title = ''
        this.getMyFollowList(44)
        break
      }
      case 'dictionary': {
        this.title = ''
        break
      }
      case 'questionList': {
        this.title = ''
        break
      }
    }
    console.log('urlParame', this.urlParame)
  }
}
</script>



