/*  eslint-disable no-irregular-whitespace  */
<template>
    <div>
      <header class="header study">
        <div class="search_box pa">
          <div class="search_main">
            <div class="return_a fl ">
              <a @click="goBack()"></a>
            </div>
            <div class="search_text">
              <em></em>
              <input type="text" name="" v-model="searchWord">
              <div v-if="searchWord !== ''" class="search_cancel fr" @click="cancelSearch">x</div>
            </div>
            <div class="search_btn fr" @click="searchDic"></div>
          </div>
        </div>
        <div class="clear"></div>
      </header>
      <com-scroll style="top:1rem">
        <div>
          <p class="text" style="text-align: left; padding-left: .2rem">{{dataExplain.word}}</p>
          <transition name="fade">
            <section class="dict_content" style="text-align: left" v-if="searchSuccess">
            <div class="translate">
              <p class="detail">
                <em class="bugle" @click="sound(dataExplain.basic.ukSpeech)"></em> [{{dataExplain.basic.ukPhonetic}}]
                <em class="bugle" @click="sound(dataExplain.basic.usSpeech)"></em> [{{dataExplain.basic.ukPhonetic}}]
              </p>
            </div>
            <div class="tab mt_30">
              <div class="tab_box">
                <ul class="tab_btn"  style="line-height: .8rem">
                  <li class="select" ></li>
                  <div class="clear"></div>
                </ul>
                <div class="tab_main">
                  <div class="tab_list">
                    <p v-for="exp in dataExplain.basic.explains" v-bind:key="exp">{{exp}}</p>
                    <p v-for="webexp in dataExplain.webExplain" v-bind:key="webexp.key"  style="height: 100%">
                      <span class="c_999 span_b"></span>
                      <span v-for="(subexp,index) in webexp.value" v-bind:key="index" style="padding-right: .1rem">{{subexp}}</span>
                      {{webexp.key}}
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div class="example" style="margin-top:0.4rem">
              <h3 class="fs_30"></h3>
              <div class="" style="margin-top:0.2rem ; font-size: .3rem">
                <p class="c_999">1 </p>
                <p class="pl_25"><span class="c_999">-:223</span></p>
                <p class="pl_25">Americans build large, efficient organizations that are not bound by the circles of kinship and <span class="c_ff6e97">clan</span>. <em class="bugle"></em> </p>
                <p class="pl_25"><span class="c_ff6e97"></span></p>
                <p class="c_999 pl_25"></p>
              </div>
            </div>
            <div class="example" style="margin-top:0.2rem ; font-size: .3rem">
              <div class="">
                <p class="c_999">2 </p>
                <p class="pl_25"><span class="c_999">-:223</span></p>
                <p class="pl_25">Americans build large, efficient organizations that are not bound by the circles of kinship and <span class="c_ff6e97">clan</span>. <em class="bugle"></em> </p>
                <p class="pl_25"><span class="c_ff6e97"></span></p>
                <p class="c_999 pl_25"></p>
              </div>
            </div>
          </section>
          </transition>
        </div>
      </com-scroll>
    </div>
</template>
<style>

  .search_box .search_main .search_text  cancel{display: inline-block;width:34px;height: 34px; position: absolute;top: 0.3rem;left: 0.1rem;background: url(../../assets/images/icon_search.png ) no-repeat;  background-size: 100%;}
</style>
<script>
/*  eslint-disable space-infix-ops  */
/*  eslint-disable no-undef  */

import ComScroll from '../../components/com_scroll.vue'
import sv from '@/service/service'
import { Indicator, Toast } from 'mint-ui'
import audio from '@/service/audio'
import {mapGetters, mapActions} from 'vuex'

export default {
  components: {
    ComScroll
  },
  data () {
    return {
      searchWord: 'clan',
      searchMode: true,
      searchSuccess: false,
      dataExplain: {
        word: '',
        basic: {
          explains: [],
          ukPhonetic: '',
          usPhonetic: '',
          ukSpeech: '',
          usSpeech: ''
        },
        webExplain: [],
        webDic: ''
      }
    }
  },
  computed: {
    ...mapGetters(
      {
        curFeature: 'currentFeature',
        curCategory: 'currentCategory',
        curLevel: 'currentLevel',
        curFid: 'currentFid',
        curHeader: 'headerType',
        preState: 'preState'
      })
  },
  watch: {
    searchWord: function (val, oldVal) {
    }
  },
  methods: {
    ...mapActions({addPreState: 'addPreState', delPreState: 'delPreState'}),
    sound: function (url) {
      if (url !== '') {
        audio.play(url)
      }
    },
    searchDic: function () {
      console.log('searchWord', this.searchWord)
      this.searchDictionary(this.searchWord)
    },
    cancelSearch: function () {
      this.searchWord = ''
      this.searchMode = false
    },
    async searchDictionary (word) {
      let params = {
        q: word
      }
      this.dataExplain.word = word
      Indicator.open({ text: 'searching...', spinnerType: 'fading-circle' })
      let svResponse = await sv.getDictionary(params)
      console.log('svResponse', svResponse)
      if (svResponse.status === 'ok') {
        if (typeof svResponse.data.attributes.basic !== 'undefined') {
          this.dataExplain.basic.explains = svResponse.data.attributes.basic.explains
          this.dataExplain.basic.ukPhonetic = svResponse.data.attributes.basic['uk-phonetic']
          this.dataExplain.basic.ukSpeech = svResponse.data.attributes.basic['uk-speech']
          this.dataExplain.basic.usPhonetic = svResponse.data.attributes.basic['us-phonetic']
          this.dataExplain.basic.usSpeech = svResponse.data.attributes.basic['us-speech']
          this.dataExplain.webExplain = svResponse.data.attributes.web
          this.dataExplain.webDic = svResponse.data.attributes.webdict.url
          this.searchSuccess = true
        } else {
          this.searchSuccess = false
          Toast({message: ''})
        }
      } else {
        this.$router.push('/login')
      }
      Indicator.close()
    },
    refreshList: function () {
      ;
    },
    goBack: function () {
      let changeState = this.preState[ this.preState.length - 1 ]
      this.delPreState(changeState)
      this.$router.back()
    }
  },
  mounted: function () {
  },
  beforeRouteLeave (to, from, next) {
    let preStare = {
      preFeature: this.curFeature,
      preFid: this.curFid,
      preLevel: this.curLevel,
      preHeader: this.curHeader,
      preCategory: this.curCategory
    }
    this.addPreState(preStare)
    next()
  }
}
</script>



