/*eslint-disable*/
import * as types from '../type';

const state = {
  loading        : false,
  currentUrl     : '',
  preUrl         : '',
  currentFeature : 'learnToSing',
  currentFid     : 43,
  currentCategory: 'chinese',
  currentLevel   : 'all',
  headerType     : 'search',
  headerContent  : '',
  layerShow      : true,
  chooseShow     : false,
  searchTags     : '',
  paperId        : '',
  currentSubject : -1,
  testDone       : true,
  preState       : [],
  currentUser    : {}
};

const getters = {
  loading        : state => state.loading,
  currentUrl     : state => state.currentUrl,
  preUrl         : state => state.preUrl,
  currentFid     : state => state.currentFid,
  currentFeature : state => state.currentFeature,
  currentCategory: state => state.currentCategory,
  currentLevel   : state => state.currentLevel,
  headerType     : state => state.headerType,
  layerShow      : state => state.layerShow,
  chooseShow     : state => state.chooseShow,
  headerContent  : state => state.headerContent,
  searchTags     : state => state.searchTags,
  paperId        : state => state.paperId,
  currentSubject : state => state.currentSubject,
  testDone       : state => state.testDone,
  preState       : state => state.preState,
  currentUser    : state => state.currentUser
};

const mutations = {
  [types.COM_LOADING_STATUS] (state, status) {
    state.loading = status;
  },
  [types.COM_CURRENT_URL] (state, url) {
    state.currentUrl = url;
  },
  [types.COM_PRE_URL] (state, url) {
    state.preUrl = url;
  },
  [types.COM_CURRENT_FID] (state, fid) {
    state.currentFid = fid;
  },
  [types.COM_CURRENT_FEATURE] (state, feature) {
    state.currentFeature = feature;
  },
  [types.COM_CURRENT_CATEGORY] (state, category) {
    state.currentCategory = category;
  },
  [types.COM_CURRENT_LEVEL] (state, level) {
    state.currentLevel = level;
  },
  [types.COM_HEADER_TYPE] (state, mode) {
    state.headerType = mode;
  },
  [types.COM_LAYER_SHOW] (state, flag) {
    state.layerShow = flag;
  },
  [types.COM_CHOOSE_LAYER_SHOW] (state, status) {
    state.chooseShow = status;
  },
  [types.COM_HEADER_CONTENT] (state, content) {
    state.headerContent = content;
  },
  [types.COM_SEARCH_TAGS] (state, tags) {
    state.searchTags = tags;
  },
  [types.COM_CURRENT_PAPER] (state, paperId) {
    state.paperId = paperId;
  },
  [types.COM_CURRENT_SUBJECT] (state, subjectId) {
    state.currentSubject = subjectId;
  },
  [types.COM_TEST_DONE] (state, testStatus) {
    state.testDone = testStatus;
  },
  [types.COM_PRE_STATE_PUSH] (state, pre) {
    state.preState.push(pre);
  },
  [types.COM_PRE_STATE_POP] (state) {
    state.preState.pop();
  },
  [types.COM_PRE_STATE_CLEAR] (state) {
    state.preState = [];
  },
  [types.COM_CUR_USER_SET] (state, userInfo) {
    state.currentUser = userInfo;
  }
};
const actions   = {
  setHeaderContent ({ commit }, content) {
    commit(types.COM_HEADER_CONTENT, content);
  },
  setLoadingState ({ commit }, status) {
    commit(types.COM_LOADING_STATUS, status);
  },
  setCurrentCategory ({ commit }, category) {
    commit(types.COM_CURRENT_CATEGORY, category);
  },
  setCurrentLevel ({ commit }, level) {
    commit(types.COM_CURRENT_LEVEL, level);
  },
  setCurrentFeature ({ commit }, feature) {
    commit(types.COM_CURRENT_FEATURE, feature.id);
    commit(types.COM_CURRENT_FID, feature.fid);
    commit(types.COM_CURRENT_CATEGORY, 'chinese');
    commit(types.COM_CURRENT_LEVEL, '1');
  },
  setCurrentFid ({ commit }, fid) {
    commit(types.COM_CURRENT_FID, fid);
  },
  setHeaderType ({ commit }, mode) {
    commit(types.COM_HEADER_TYPE, mode);
  },
  setLayerStatus ({ commit }, flag) {
    commit(types.COM_LAYER_SHOW, flag);
  },
  setChooseLayerStatus ({ commit }, flag) {
    commit(types.COM_CHOOSE_LAYER_SHOW, flag);
  },
  setSearchTags ({ commit }, tags) {
    commit(types.COM_SEARCH_TAGS, tags);
  },
  setPaperId ({ commit }, id) {
    commit(types.COM_CURRENT_PAPER, id);
  },
  setCurrentSubjectID ({ commit }, id) {
    commit(types.COM_CURRENT_SUBJECT, id);
  },
  setTestStatus ({ commit }, status) {
    commit(types.COM_TEST_DONE, status);
  },
  addPreState ({ commit }, pre) {
    commit(types.COM_PRE_STATE_PUSH, pre);
  },
  delPreState ({ commit }, pre) {
    commit(types.COM_CURRENT_FEATURE, pre.preFeature);
    commit(types.COM_CURRENT_CATEGORY, pre.preCategory);
    commit(types.COM_CURRENT_FID, pre.preFid);
    commit(types.COM_CURRENT_LEVEL, pre.preLevel);
    commit(types.COM_HEADER_TYPE, pre.preHeader);
    commit(types.COM_PRE_STATE_POP);
  },
  clearPreState ({ commit }) {
    commit(types.COM_PRE_STATE_CLEAR);
  },
  setCurUser ({ commit }, userInfo) {
    commit(types.COM_CUR_USER_SET, userInfo);
  }
};

export default {
  state,
  getters,
  mutations,
  actions
};



