<template>
  <div class="player">
    <video-player  class="vjs-custom-skin"
                   ref="videoPlayer"
                   :options="playerOptions"
                   :playsinline="true"
                   @play="onPlayerPlay($event)"
                   @pause="onPlayerPause($event)"
                   @ended="onPlayerEnded($event)"
                   @loadeddata="onPlayerLoadeddata($event)"
                   @waiting="onPlayerWaiting($event)"
                   @playing="onPlayerPlaying($event)"
                   @timeupdate="onPlayerTimeupdate($event)"
                   @canplay="onPlayerCanplay($event)"
                   @canplaythrough="onPlayerCanplaythrough($event)"
                   @ready="playerReadied"
                   @statechanged="playerStateChanged($event)">
    </video-player>
    <!---->
    <div class="video_btn video_btn01 top-button" style="z-index:2">
      <a @click="goBack" class="fl"><img src="../assets/images/video_icon06.png" width="100%"></a>
      <a class="fr"><img src="../assets/images/video_icon08.png" width="100%"></a>
      <a class="fr" @click="markedMedia"><img src="../assets/images/video_icon07.png" width="100%"></a>
      <div class="clear"></div>
    </div>
    <!-- end-->
    <div class="video container">
      <video-info :videoInfo="videoInfo" v-if="videoInfo"></video-info>
    </div>
  </div>
</template>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .top-button
    position absolute
    width 100%
    top -.2rem
  .vjs-custom-skin
    .video-js
      height 4.5rem
</style>
<script>

import VideoInfo from '@/components/com_video_info.vue'
import sv from '@/service/service'
import { Toast } from 'mint-ui'
export default {
  components: {
    VideoInfo
  },
  data () {
    return {
      needPay: false,
      videoInfo: null,
      maId: '',
      // videojs options
      playerOptions: {
        height: '300',
        autoplay: false,
        muted: true,
        language: 'en',
        playbackRates: [0.7, 1.0, 1.5, 2.0],
        sources: [{
          type: 'video/mp4',
          // mp4
          src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm'
          // webm
          // src: "https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm"
        }],
        poster: ''
      }
    }
  },
  computed: {
    //    player () {
    //      console.log(this.$refs)
    //      return this.$refs.videoPlayer.player
    //    }
  },
  methods: {
    goBack () {
      this.$router.back()
    },
    async markedMedia () {
      let mediaid = this.$route.params.url
      let svResponse = await sv.patchMarkedMedia(mediaid)
      if (svResponse.status === 'ok') {
        console.log('svResponse')
      } else {
        this.$router.push('/login')
      }
    },
    // listen event
    onPlayerPlay (player) {
      if (this.needPay) {
        Toast({message: '', duration: 1000})
        player.pause()
        return
      }
      console.log('player play!', player)
    },
    onPlayerPause (player) {
      console.log('player pause!', player)
    },
    onPlayerEnded (player) {
      console.log('player ended!', player)
    },
    onPlayerLoadeddata (player) {
      console.log('player Loadeddata!', player)
    },
    onPlayerWaiting (player) {
      console.log('player Waiting!', player)
    },
    onPlayerPlaying (player) {
      console.log('player Playing!', player)
    },
    onPlayerTimeupdate (player) {
      if (this.needPay) {
        Toast({message: '', duration: 1000})
        player.pause()
        return
      }
      console.log('player Timeupdate!', player.currentTime())
    },
    onPlayerCanplay (player) {
      console.log('player Canplay!', player)
    },
    onPlayerCanplaythrough (player) {
      console.log('player Canplaythrough!', player)
    },
    // or listen state event
    playerStateChanged (playerCurrentState) {
      console.log('player current update state', playerCurrentState)
    },
    // player is ready
    playerReadied (player) {
      // seek to 10s
      console.log('example player 1 readied', player)
      //      player.currentTime(10)
      // console.log('example 01: the player is readied', player)
    },
    async getVideo () {
      let params = {
        maId: this.maId
      }
      console.log(params)
      let svResponse = await sv.getVideo(params)
      if (svResponse.status === 'ok') {
        if (svResponse.data.attributes.is_payed === '0') {
          this.needPay = true
        }
        this.playerOptions = {
          sources: [{
            type: 'video/mp4',
            // mp4
            src: svResponse.data.attributes.mediasource[0]
          }],
          poster: svResponse.data.attributes.cover
        }
        this.videoInfo = {
          title: svResponse.data.attributes.title,
          info: '',
          score: 9.8
        }
        //   
        let html = document.getElementsByTagName('html')[0]
        let fontSize = html.style.fontSize.slice(0, html.style.fontSize.length - 2)
        this.playerOptions.height = fontSize * 4.5
      } else {
        this.$router.push('/login')
      }
    }
  },
  mounted: function () {
    let html = document.getElementsByTagName('html')[0]
    let fontSize = html.style.fontSize.slice(0, html.style.fontSize.length - 2)
    this.playerOptions.height = fontSize * 4.5
    this.maId = this.$route.params.url
    this.getVideo()
    setTimeout(() => {
      console.log('dynamic change options', this.player)
      // change src
      // this.playerOptions.sources[0].src = 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm';
      // change item
      // this.$set(this.playerOptions.sources, 0, {
      //   type: "video/mp4",
      //   src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm',
      // })
      // change array
      // this.playerOptions.sources = [{
      //   type: "video/mp4",
      //   src: 'https://cdn.theguardian.tv/webM/2015/07/20/150716YesMen_synd_768k_vp8.webm',
      // }]
      this.player.muted(false)
    }, 5000)
  }
}
</script>



