<template>
      <div class="layer" style="position: fixed;">
        <div class="ready_pic" v-if="testStatus === false">
          <p><img src="../assets/images/ready_pic.png"></p>
          <p><img @click="setOK(false)" src="../assets/images/btn01.png">&nbsp;<img @click="cancelAnswer(false)" src="../assets/images/btn02.png"></p>
        </div>
        <section class="section  study container mt_40" style="padding-bottom: 1rem; margin-top: 1rem" v-else>
          <div class="score score_bg">
            <h3 class="fs_36 tc" style="padding-top: .5rem"></h3>
            <div class="score_box tc">
              <div class="score_num">{{score}}</div>
              <div class="score_star" style="height: 2.2rem">
                <img class="imgPos" src="../assets/images/star3.png">
                <img class="imgPos" src="../assets/images/star2.png">
                <img class="imgPos" src="../assets/images/star1.png">
                <img class="imgPos" src="../assets/images/star2.png">
                <img class="imgPos" src="../assets/images/star3.png">
              </div>
              <div class="score_ratio"><span>{{score}}%</span></div>
              <div class="score_text" style="position: relative;top: -.3rem;">88%</div>
            </div>
            <div class="score_msg">
              <div class="w1 tc">
                <p></p>
                <p>{{completeTime}}</p>
                <p class="score_btn" @click="completeTest"><img src="../assets/images/score_btn01.png"></p>
              </div>
              <div class="w1 tc">
                <p></p>
                <p>{{wrongNum}}</p>
                <p class="score_btn" @click="viewWrong"><img src="../assets/images/score_btn02.png"></p>
              </div>
            </div>
            <div class="clear"></div>
          </div>
        </section>
      </div>
</template>
<style scoped lang="stylus" rel="stylesheet/stylus">
  .shadow
    width: 100%
    height: 100%
    background-color rgba(6, 6, 6, 0.8)
    position absolute
    top 0
    z-index 1001
  .imgPos
    position: relative;
    top: -.5rem;
</style>
<script>
import {mapGetters, mapActions} from 'vuex'
export default {
  props: {
    testStatus: {
      type: Boolean,
      default: false
    },
    score: {
      type: Number,
      default: 0
    },
    wrongNum: {
      type: Number,
      default: 0
    },
    completeTime: ''
  },
  components: {},
  data () {
    return {
    }
  },
  computed: {
    ...mapGetters({cureentPaper: 'paperId'})
  },
  methods: {
    ...mapActions(
      {setLayerStatus: 'setLayerStatus', setTestStatus: 'setTestStatus'}
    ),
    setOK (val) {
      this.setLayerStatus(val)
      this.setTestStatus(false)
    },
    cancelAnswer: function () {
      this.$router.back()
      this.setLayerStatus(true)
    },
    completeTest: function () {
      this.$router.back()
    },
    viewWrong: function () {
      this.setLayerStatus(false)
    }
  },
  mounted: function () {
  }
}
</script>



