<template>
  <div id="app">
    <transition :name="animateMode">
        <router-view>
        </router-view>
    </transition>
  </div>
</template>

<script>
import HomePage from '@/pages/homePage/HomePage.vue'
import { mapGetters } from 'vuex'
/* eslint-disable */
export default {
  name: 'App',
  data () {
    return {
      animateMode: 'fade'
    }
  },
  components: {
    HomePage
  },
  computed: {
    ...mapGetters(['loading'])
  },
  created: function () {
//    document.body.addEventListener('touchstart', function () {
//      console.log('touch start app.vue')
//    })
//    console.log('app start')
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

body {
  position: absolute;
  height: 100%;
  width: 100%;
  margin: 0px;
  top: 0px;
}
</style>



