<template>
    <div>
      <div class="swipe-wrapper">
        <mt-swipe :auto="0" ref="swipeWrapper" :show-indicators="false">
          <mt-swipe-item class="item" v-bind:key="picItem.picRes" v-for="picItem in picList">
          <!--<router-link :to="picItem.link">-->
            <img style="height: 100%; max-width: 100%" :src="picItem.picRes"/>
          <!--</router-link>-->
          </mt-swipe-item>
        </mt-swipe>
        <div class="button-wrapper" v-show="button_show">
          <button class="prev-button flex-item" @click="prev">prev</button>
          <button class="next-button flex-item" @click="next">next</button>
        </div>
      </div>
    </div>
</template>
<style scoped="">
  .swipe-wrapper{
    width: 100%;
    height: 5.2rem;
  }
  .item{
    text-align: center;
    font-size: 1rem;
    color: white;
  }
  .item img{
    width: 100%;
  }

  .button-wrapper{
    position: absolute;
    top: 100px;
    display: flex;
    width: 100%;
    height: 1rem;
    justify-content: space-between;
  }
  .flex-item{
    display: inline-block;
    text-align: center;
    height: 100%;
    line-height: 100%;
    font-size: .4rem;
  }
  .prev-button{
    background: darkorange;
  }
  .next-button{
    background: green;
  }

</style>
<script>
import sv from '@/service/service'

export default {
  props: {
    button_show: {
      type: Boolean,
      default: false
    }
  },
  components: {},
  data () {
    return {
      picList: []
    }
  },
  methods: {
    prev: function () {
      this.$refs.swipeWrapper.prev()
      console.log(this.$children)
    },
    next: function () {
      this.$refs.swipeWrapper.next()
    },
    async getAdvertisement () {
      let svResponse = await sv.getAdvertisement()
      if (svResponse.status === 'ok') {
        this.picList = svResponse.data[0].attributes.images.map((item) => {
          return {
            link: '',
            picRes: item
          }
        })
      } else {
        this.$router.push('/login')
      }
    }
  },
  mounted: function () {
    this.getAdvertisement()
  }
}
</script>



