<template>
  <div class="homePage">
    <com-header></com-header>
    <transition name="fade">
      <router-view :key="key"></router-view>
    </transition>
    <com-footer ref="footer_nav"></com-footer>
  </div>
</template>

<style>
  .guide {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2000;
  }
</style>
<script>
/* eslint-disable-next-line */

import ComHeader from '../../components/com_header.vue'
import PLearnToSing from './p_learnToSing.vue'
import ComFooter from '../../components/com-footerNavigation.vue'

export default {
  data () {
    return {}
  },
  components: {
    ComHeader, PLearnToSing, ComFooter
  },
  computed: {
    key () {
      return this.$route.name !== undefined ? this.$route.name + new Date() : this.$route + new Date()
    }
  },
  mounted: function () {
    console.log('')
    setTimeout(() => { this.$router.push({name: 'learnToSing'}) }, 1000)
  }
}
</script>



