<template>
    <div class="navbar">
      <mt-navbar v-model="categorySel">
        <mt-tab-item v-for="category in categoryList" :key="category.id" :id="category.fid"><span>{{category.title}}</span></mt-tab-item>
      </mt-navbar>
      <!-- tab-container -->
      <div class="level-classification fs_26" >
        <mt-button v-for="level in levelList" :key="level.id" class="levelButton" :class="[(levelSel === level.difficulty) ? 'levelButtonSel' : 'levelButton']" @click="activeButton(level)"  >{{level.title}}</mt-button>
      </div>
    </div>
</template>
<style scoped="">
  .navbar{
    margin-top: .2rem;
    z-index: 999;
  }
  .navbar .mint-navbar{
    background-color: transparent;
  }
  .navbar .mint-navbar .mint-tab-item.is-selected {
    border-bottom: .03rem solid hotpink;
    color: hotpink;
    margin-bottom: -.03rem ;
  }
  .navbar .mint-navbar .mint-tab-item .mint-tab-item-label >span {
    font-size: .3rem ;
    line-height: 1;
  }
  .level-classification {
    height: .8rem;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .levelButton{
    display: inline-block;
    font-size: .26rem;
    height: .4rem;
    top: 20%;
  }
  .levelButtonSel{
    background-color: hotpink;
    color: aliceblue;
  }
</style>
<script>
import {mapGetters, mapActions} from 'vuex'
import sv from '../service/service'
import fid from '../service/fid'

export default {
  components: {},
  data () {
    return {
      fidSel: 45,
      categorySel: '',
      levelSel: 1,
      categoryList: [
        {
          title: ''
        }, {
          title: ''
        }, {
          title: ''
        }, {
          title: ''
        }
      ],
      levelList: [
        {
          title: '',
          id: 'enlightenment',
          difficulty: 1
        }, {
          title: '',
          id: 'basis',
          difficulty: 2
        }, {
          title: '',
          id: 'mprove',
          difficulty: 3
        }, {
          title: '',
          id: 'other',
          difficulty: 4
        }
      ]
    }
  },
  computed: {
    ...mapGetters({curFeature: 'currentFeature', curCategory: 'currentCategory', curLevel: 'currentLevel', curFid: 'currentFid'})
  },
  methods: {
    ...mapActions(
      {
        setCurrentLevel: 'setCurrentLevel',
        setCurrentCategory: 'setCurrentCategory',
        setCurrentFid: 'setCurrentFid'
      }),
    activeButton: function (level) {
      this.levelSel = level.difficulty
    },

    async getSubFeatures (fid) {
      let svResponse = await sv.getSubFeatures(fid)
      if (svResponse.status === 'ok') {
        this.categoryList = svResponse.data.attributes.children.map(
          ({title, fid, icon}) => {
            return {
              title: title,
              fid: fid,
              picRes: icon
            }
          }
        )
        console.log('this.categoryList', this.categoryList)
        if (this.categoryList.length === 0) {
          this.categoryList = [ {
            title: '',
            id: 'chinese',
            fid: '98'
          }, {
            title: '',
            id: 'english',
            fid: '99'
          }, {
            title: '',
            id: 'math',
            fid: '97'
          }, {
            title: '',
            id: 'encyclopedia',
            fid: '100'
          }]
        }
      } else {
        this.$router.push('/login')
      }
      this.categorySel = this.curFid
    }
  },
  watch: {
    categorySel: function (val, oldVal) {
      this.levelSel = this.levelList[0].difficulty
      this.setCurrentCategory(val)
      this.setCurrentFid(val)
    },
    levelSel: function (val, oldVal) {
      this.setCurrentLevel(val)
    }
  },
  mounted: function () {
    let curFeatureFid = fid.getFid(this.curFeature)
    console.log('this.curFeatureFid', curFeatureFid)
    this.getSubFeatures(curFeatureFid)
  }
}
</script>



