<template>
    <div class="chooseSubject">
      <section class="errors" style="width: 100%;height: 100%">
        <div class="errors_count">
          <div class="counts count1" v-if="testDone"><img :src="rightPic"> <span  class="c_07c25b">{{userAnswers.length - wrongNum}}</span></div>
          <div class="counts count2" v-if="testDone"><img :src="wrongPic"> <span  class="c_ff3535">{{wrongNum}}</span></div>
          <div class="counts count3"><img :src="allPic"> {{userAnswers.length}}</div>
          <div class="counts count4" @touchstart="setChooseLayerStatus(false)"><img :src="hidePic"> </div>
        </div>
        <ul class="errors_list mt_10 subjectList"  v-if="testDone">
          <li :class="[item === rightAnswers[index].answer.toUpperCase() ? 'right' : 'wrong']" v-for="(item,index) in userAnswers" v-bind:key="index" @click="chooseSubject(item,index)">
            {{typeof  item !== 'undefined' ? item : index+1 }}
          </li>
        </ul>
        <ul class="errors_list mt_10 subjectList"  v-else>
          <li :class="[typeof  item !== 'undefined' ? 'done' : 'todo']" v-for="(item,index) in userAnswers" v-bind:key="index" @click="chooseSubject(item,index)">
            {{typeof  item !== 'undefined' ? item : index+1 }}
          </li>
        </ul>
        <div class="clear"></div>
      </section>
    </div>
</template>
<style scoped="">
.chooseSubject {
  z-index: 1003;
  background-color: #f8f7ff;
  position: absolute;
  height: 8rem;
  bottom: 0rem;
  width: 100%;
}
.chooseSubject .errors .subjectList{
  overflow: scroll;
  height:7rem;
  display: flex;
  flex-wrap: wrap;
  padding: 0 .1rem;
}
</style>
<script>
import rightSubject from '@/assets/images/icon_test01.png'
import wrongSubject from '@/assets/images/icon_test02.png'
import allSubject from '@/assets/images/icon_test03.png'
import hideSubject from '@/assets/images/icon_test04.png'
import {mapActions, mapGetters} from 'vuex'

export default {
  props: {
    userAnswers: {
      type: Array,
      default: new Array('')
    },
    rightAnswers: {
      type: Array,
      default: new Array('')
    },
    testDone: {
      type: Boolean,
      default: false
    },
    wrongNum: {
      type: Number,
      default: 0
    }
  },
  components: {},
  data () {
    return {
      subjectList: [],
      rightPic: rightSubject,
      wrongPic: wrongSubject,
      allPic: allSubject,
      hidePic: hideSubject
    }
  },
  computed: {
    ...mapGetters(
      { currentSubject: 'currentSubject' }
    )
  },
  methods: {
    ...mapActions(
      {setChooseLayerStatus: 'setChooseLayerStatus', setCurrentSubjectID: 'setCurrentSubjectID'}
    ),
    chooseSubject (item, index) {
      this.setChooseLayerStatus(false)
      this.setCurrentSubjectID(index)
    }
  },
  mounted: function () {
  }
}
</script>



