<template>
  <div>
      <com-scroll ref="scroll" :refreshDelay="300">
        <div>
          <com-swiper></com-swiper>
          <com-button-list></com-button-list>
          <keep-alive>
              <com-content-list :width="95" :limit="showItemNum"  :mode="'posttime'"></com-content-list>
          </keep-alive>
          <keep-alive>
              <com-content-list :width="145" :limit="showItemNum" :mode="'hits'"></com-content-list>
          </keep-alive>
        </div>
      </com-scroll>
  </div>
</template>
<style scoped="">
</style>
<script>
import ComScroll from '../../components/com_scroll.vue'
import ComSwiper from '../../components/com_swiper.vue'
import ComButtonList from '../../components/com_category_button.vue'
import ComContentList from '../../components/com_content_list.vue'
import {mapGetters, mapActions} from 'vuex'

export default {
  data () {
    return {
      showItemNum: 6
    }
  },
  components: {
    ComSwiper, ComButtonList, ComContentList, ComScroll
  },
  computed: {
    ...mapGetters({curFeature: 'currentFeature', curCategory: 'currentCategory', curLevel: 'currentLevel', curFid: 'currentFid', curHeader: 'headerType'})
  },
  methods: {
    ...mapActions({addPreState: 'addPreState', delPreState: 'delPreState'})
  },
  created: function () {
  },
  mounted: function () {
    console.log(this)
  },
  beforeRouteLeave (to, from, next) {
    let preStare = {
      preFeature: this.curFeature,
      preFid: this.curFid,
      preLevel: this.curLevel,
      preHeader: this.curHeader,
      preCategory: this.curCategory
    }
    this.addPreState(preStare)
    next()
  }
}
</script>



