<template>
    <div class="study" style="height: 2.2rem">
      <ul class="nav" >
          <li class="swiper-slide" v-for="button in buttonList" :key="button.category" @click="gotoAddress(button)">
            <router-link :to="button.link + '/category'">
              <p style="height: 1.1rem"><img :src="button.picRes"></p>
              <p class="text">{{button.title}}</p>
            </router-link>
          </li>
      </ul>
    </div>
</template>
<style scoped="">
</style>
<script>
import chinesePic from '../assets/images/study-05.png'
import englishPic from '../assets/images/study-07.png'
import mathPic from '../assets/images/study-11.png'
import encyclopediaPic from '../assets/images/study-13.png'
import {mapGetters, mapActions} from 'vuex'
import sv from '../service/service'

export default {
  components: {},
  data () {
    return {
      buttonList: [
        {
          title: '',
          category: 'chinese',
          fid: 45,
          picRes: chinesePic,
          link: ''
        },
        {
          title: '',
          category: 'english',
          fid: 46,
          picRes: englishPic,
          link: ''
        },
        {
          title: '',
          category: 'math',
          fid: 47,
          picRes: mathPic,
          link: ''
        },
        {
          title: '',
          category: 'encyclopedia',
          fid: 48,
          picRes: encyclopediaPic,
          link: ''
        }
      ]
    }
  },
  computed: {
    ...mapGetters({curFeature: 'currentFeature', curUrl: 'currentUrl', curFid: 'currentFid'})
  },
  methods: {
    ...mapActions(
      {
        setHeaderType: 'setHeaderType',
        setCurrentFid: 'setCurrentFid'
      }
    ),
    gotoAddress: function (button) {
      this.setHeaderType('search')
      this.setCurrentFid(button.fid)
      this.$router.push(button.link + '/category')
    },
    async getSubFeatures (fid) {
      let svResponse = await sv.getSubFeatures(fid)
      if (svResponse.status === 'ok') {
        this.buttonList = []
        this.buttonList = svResponse.data.attributes.children.map(
          ({title, fid, icon}) => {
            return {
              title: title,
              fid: fid,
              picRes: icon,
              link: `/homePage/${this.curFeature}/sing`
            }
          }
        )
      } else {
        this.$router.push('/login')
      }
    }
  },
  mounted: function () {
    this.getSubFeatures(this.curFid)
  }
}
</script>



