      </div>
    </section>
  </div>
</template>
<style>

</style>
<script>
import ComReturn from '@/components/com_return.vue'
export default {
  components: {
    ComReturn
  },
  data () {
    return {
      title: '',
      buttonShow: false
    }
  },
  methods: {},
  mounted: function () {
  },
  beforeRouteLeave (to, from, next) {
    document.body.removeAttribute('class', 'bg03')
    next()
  }
}
</script>



