import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import Guide from '@/pages/homePage/Guide'
import Home from '@/pages/homePage/HomePage'
import PLearnToSing from '@/pages/homePage/p_learnToSing'
import PLearnSing2 from '@/pages/homePage/LevelTwoPage/p_learn_sing_2'
import PLearnSing3 from '@/pages/homePage/LevelThreePage/p_learn_sing_3'
import PFollowSing3 from '@/pages/homePage/LevelThreePage/p_follow_sing_detail'
import PExamination3 from '@/pages/homePage/LevelThreePage/p_examination_detail'
import PProfile3 from '@/pages/homePage/LevelThreePage/p_profile_detail'
import PRankList from '@/pages/homePage/p_rankList'
import UserProfile from '@/pages/homePage/p_user_profile'
import Dictionary from '@/pages/homePage/p_dictionary'
import PAboutUs from '@/pages/homePage/LevelThreePage/p_system_about_us'
import PChangeIcon from '@/pages/homePage/LevelThreePage/p_system_change_icon'
import PChangeName from '@/pages/homePage/LevelThreePage/p_system_change_name'
import PInvitation from '@/pages/homePage/LevelThreePage/p_system_invitation'
import PPassword from '@/pages/homePage/LevelThreePage/p_system_password'
import PSuggestion from '@/pages/homePage/LevelThreePage/p_system_suggestion'

Vue.use(Router)

/* eslint-disable-next-line */
const router = new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/guide'
    },
    {
      path: '/guide',
      name: 'Guide',
      component: Guide,
      beforeEnter (to, from, next) {
        //    console.log(' document.getElementsByTagName(\'body\')[0]', document.getElementsByTagName('body')[0])
        document.getElementsByTagName('body')[0].className = 'bg05 loading'
        next()
      }
    },
    {
      path: '/login',
      name: 'Lgoin',
      component: Login,
      beforeEnter (to, from, next) {
        //    console.log(' document.getElementsByTagName(\'body\')[0]', document.getElementsByTagName('body')[0])
        document.getElementsByTagName('body')[0].className = 'bg03'
        next()
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/homePage',
      component: Home,
      children: [
        {
          path: 'dictionary',
          name: 'Dictionary',
          component: Dictionary
        },
        {
          path: 'profile',
          name: 'User',
          component: UserProfile
        },
        {
          name: 'learnToSing',
          path: 'learnToSing',
          component: PLearnToSing
        },
        {
          name: 'followToSing',
          path: 'followToSing',
          component: PLearnToSing
        },
        {
          name: 'rankList',
          path: 'rankList/:user',
          component: PRankList
        },
        {
          name: 'songrankList',
          path: 'rankList/song/:songname',
          component: PRankList
        },
        {
          name: 'examination',
          path: 'examination',
          component: PLearnSing2
        },
        {
          name: 'detail-examination',
          path: 'examination/detail/:url',
          component: PExamination3
        },
        {
          name: 'detail-profile',
          path: 'profile/:url',
          component: PProfile3
        },
        {
          path: '/',
          component: null
        },
        {
          path: 'learnToSing/sing/category',
          component: PLearnSing2
        },
        {
          name: 'detail-learnToSing',
          path: 'learnToSing/sing/detail/:url',
          component: PLearnSing3
        },
        {
          path: 'followToSing/sing/:category',
          component: PLearnSing2
        },
        {
          name: 'detail-followToSing',
          path: 'followToSing/sing/detail/:url',
          component: PFollowSing3
        },
        {
          name: 'system-aboutUs',
          path: 'system/aboutus',
          component: PAboutUs
        },
        {
          name: 'system-invitation',
          path: 'system/invitation',
          component: PInvitation
        },
        {
          name: 'system-suggestion',
          path: 'system/suggestion',
          component: PSuggestion
        },
        {
          name: 'system-password',
          path: 'system/password',
          component: PPassword,
          beforeEnter (to, from, next) {
            //    console.log(' document.getElementsByTagName(\'body\')[0]', document.getElementsByTagName('body')[0])
            document.getElementsByTagName('body')[0].className = 'bg03'
            next()
          }
        },
        {
          name: 'system-changeIcon',
          path: 'system/changeicon:url',
          component: PChangeIcon
        },
        {
          name: 'system-changeName',
          path: 'system/changename',
          component: PChangeName
        }
      ]
    }
  ]
})

export default router



