<template>
  <div>
    <mt-header v-if="headerType === 'header' ? true : false" :title="headerContent" style="background-color: transparent ;color: hotpink;height: .8rem">
         <div  @click="goBack" class="return_goback" style="margin-left: .2rem;" slot="left"></div>
    </mt-header>
    <div v-if="headerType === 'search' ? true : false">
      <header class="search-header">
        <!--<router-link :to="preUrl" slot="left">-->
         <div class="return_goback" @click="goPreBack()"></div>
        <!--</router-link>-->
        <mt-search style="height: 100%;  flex-grow: 9" ref="headerSearch"
                   v-model="searchWord" @focus.native.capture="showSearchPage()"  @click.native.capture="hideSearchPage"
                   cancel-text=""
                   placeholder="" slot="right">
        </mt-search>
      </header>
      <transition name="slide-fade">
        <div class="search_layer" v-show="startSearch">
          <div class="content search_hot">
            <h3>++</h3>
            <br>
            <ul class="search_list">
              <li v-for="item in searchTags" :key="item.id" @click="gotoAddress(item)" :class="{'isHv': item.isHv}"
                  @mouseover="isHover(item)" @mouseout="notHover(item)">
                {{item.name}}
              </li>
              <div class="clear"></div>
            </ul>
          </div>
          <div class="content search_hot mt_10">
            <h3><a href="" class="fr mt_10"><img src="../assets/images/icon_delete.png"></a></h3>
            <div class="search_list mt_10">
              <p class="lh60"></p>
              <p class="lh60"></p>
            </div>
          </div>
      </div>
      </transition>
      <transition name="slide-fade">
        <div class="search_tips layer" style="top: .9rem;" v-show="searchTips">
          <div class="content pre-search" style="font-size: .25rem">
            <div class="tips_list"> <a href="" class="follow_a" style="margin: 0 .3rem"></a><span class="c_ff6e97"></span></div>
            <div class="tips_list"> <a href="" class="follow_a" style="margin: 0 .3rem"></a><span class="c_ff6e97"></span></div>
            <div class="tips_list"> <a href="" class="follow_a" style="margin: 0 .3rem"></a><span class="c_ff6e97"></span></div>
            <div class="tips_list"> <a href="" class="follow_a" style="margin: 0 .3rem"></a><span class="c_ff6e97"></span></div>
            <div class="tips_list"> <a href="" class="follow_a" style="margin: 0 .3rem"></a><span class="c_ff6e97"></span></div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>
<style>
  .search-header {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    box-sizing: border-box;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: .2rem;
    height: .8rem;
    line-height: 1;
    padding: 0 .1rem;
    position: relative;
    text-align: center;
    white-space: nowrap;
  }
  .search-header .mint-search .mint-searchbar {
    background-color: transparent;
    height: .7rem;
    font-size: .3rem;
    margin: .05rem .1rem;
  }
  .search-header .mint-search .mint-searchbar .mint-searchbar-inner {
    height: .7rem;
    font-size: .3rem;
  }
  .search-header .mint-search .mint-searchbar .mint-searchbar-inner {
    height: .6rem;
    font-size: .3rem;
  }
  .search-header .mint-search .mint-searchbar .mint-searchbar-inner .mint-searchbar-core{
    font-size: .3rem;
    text-indent: .2rem;
  }
  .mint-searchbar-inner .mintui-search {
    font-size: .4rem;
    color: #d9d9d9;
  }
  .search-header .mint-search .mint-searchbar .mint-searchbar-cancel{
    color: #777777;
  }
  .pre-search{
    margin-top: 0; font-size: .2rem; text-align: left
  }
  .mint-header-title{
    font-size: .3rem;
    font-weight: bold;
  }
  .isHv {
    background-color: hotpink;
    color:#fff
  }
</style>
<script>
import {mapGetters, mapActions} from 'vuex'
import sv from '@/service/service'

export default {
  components: {},
  data () {
    return {
      mode: 'header',
      searchWord: '',
      searchResult: '',
      startSearch: false,
      searchTips: false,
      searchTags: []
    }
  },
  computed: {
    ...mapGetters({headerType: 'headerType', headerContent: 'headerContent', preState: 'preState'})
  },
  watch: {
    searchWord: function (val, oldVal) {
      if (val !== '') {
        this.searchTips = true
      } else {
        this.searchTips = false
      }
    }
  },
  methods: {
    ...mapActions(
      {
        setSearchTags: 'setSearchTags',
        delPreState: 'delPreState'
      }),
    isHover (item) {
      console.log('isHover')
      item.isHv = true
    },
    notHover (item) {
      console.log('notHover')
      item.isHv = false
    },
    goBack () {
      let changeState = this.preState[this.preState.length - 1]
      console.log('changeState', changeState)
      this.delPreState(changeState)
      this.$router.back()
    },
    goPreBack () {
      let changeState = this.preState[this.preState.length - 1]
      console.log('changeState', changeState)
      this.delPreState(changeState)
      this.$router.back()
    },
    gotoAddress (tags) {
      this.setSearchTags(tags.name)
      setTimeout(() => {
        this.startSearch = false
      }, 0)
    },
    showSearchPage () {
      this.startSearch = true
      this.getTags()
    },
    hideSearchPage () {
      setTimeout(() => {
        if (this.$refs.headerSearch.visible === false) {
          this.startSearch = false
        }
      }, 0)
    },
    async getTags () {
      let svResponse = await sv.getTags()
      if (svResponse.status === 'ok') {
        this.searchTags = svResponse.data.map((item) => {
          return {
            name: item.id,
            link: item.links.self,
            isHv: false
          }
        })
      }
    }
  },
  mounted: function () {
  }
}
</script>



