<template>
    <div>
      <div style="font-size: .4rem; color: aliceblue; font-weight: bold">{{songTitle}}</div>
      <mt-range v-model="currentTimeLine" style="margin:.3rem .4rem; font-size: .3rem ;color:#f8f8f8;" @change="changeTimeLine"
                @touchstart.native.capture="preDrag"  @touchend.native.capture="endDrag">
        <div slot="start">{{startTime}}</div>
        <div slot="end">{{endTime}}</div>
      </mt-range>
      <div class="paly">
        <audio id="audioControl" src="http://sc1.111ttt.cn:8282/2018/1/03m/13/396131229550.m4a?tflag=1519095601&pin=6cd414115fdb9a950d827487b16b5f97#.mp3"
               autoplay="autoplay" ref="player" preload="true" @ended="endPlay" @timeupdate='playerTimeUpdate' @canplay="canPlay">
        </audio>
      </div>
      <div class="progress alpha">
        <!---->
        <div class="link_86435e" v-bind:style="{ width: currentTimeLine + '%' }"></div>
        <!-- end-->
        <div class="grade" v-bind:style="{ left: currentTimeLine-20 + '%' }"><span>B</span>82</div>
        <!--<div class="line_span">-->
        <!--<span class=" w1"></span>-->
        <!--<span class=" w2"></span>-->
        <!--<span class=" w3"></span>-->
        <!--<span class=" w4"></span>-->
        <!--<span class=" w5"></span>-->
        <!--<span class=" w6"></span>-->
        <!--</div>-->
      </div>
      <div class="header study " style="padding-top: .5rem">
        <div class="play_content">
          <transition-group name="list" tag="span">
            <span v-for="(item) in showLrc" v-bind:key="item.no" class="list-item">
              <em v-if="item.text === showText "></em>
              <span :class="[item.text === showText? 'cur_text' : '']">{{item.text}}</span>
            </span>
          </transition-group>
        </div>
        <!-- Add Pagination -->
        <div class="swiper-pagination"></div>
      </div>
    </div>
</template>
<style>
.mt-range-content .mt-range-progress  {
  background-color: hotpink;
}

.mt-range-content  .mt-range-thumb {
  background-color: hotpink;
}
.mt-range >div {
  padding: 0rem .1rem;
}
</style>
<script>
/* eslint-disable no-useless-escape */
/* eslint-disable no-multi-spaces */
/* eslint-disable operator-linebreak */
export default {
  components: {},
  data () {
    return {
      songTitle: '',
      rangeValue: 0,
      au: null,
      isPlaying: true,
      currentTimeLine: 0,
      isDrag: false,
      startTime: 0,
      endTime: 0,
      lrcObj: {},
      showText: '',
      showLrc: [],
      lastTextIndex: 0,
      lrc: '[ar:]\n'
           + '\n'
           + '[ti:]\n'
           + '\n'
           + '[00:02.21] - \n'
           + '\n'
           + '[00:03.68]NZBZ\n'
           + '\n'
           + '[00:04.99]NZBZ\n'
           + '\n'
           + '[00:06.41]NZBZ\n'
           + '\n'
           + '[00:08.09]\n'
           + '\n'
           + '[00:09.31]\n'
           + '\n'
           + '[00:20.37]\n'
           + '\n'
           + '[00:22.80]\n'
           + '\n'
           + '[00:25.09]\n'
           + '\n'
           + '[00:26.92]\n'
           + '\n'
           + '[00:29.91]\n'
           + '\n'
           + '[00:32.20]\n'
           + '\n'
           + '[00:34.67]\n'
           + '\n'
           + '[00:39.54]\n'
           + '\n'
           + '[00:41.87]\n'
           + '\n'
           + '[00:44.35]\n'
           + '\n'
           + '[00:49.30]\n'
           + '\n'
           + '[00:51.48]\n'
           + '\n'
           + '[00:53.85]\n'
           + '\n'
           + '[00:58.73]\n'
           + '\n'
           + '[01:01.26]\n'
           + '\n'
           + '[01:03.64]\n'
           + '\n'
           + '[01:08.23]\n'
           + '\n'
           + '[01:10.66]\n'
           + '\n'
           + '[01:13.04]\n'
           + '\n'
           + '[01:17.96]\n'
           + '\n'
           + '[01:20.30]\n'
           + '\n'
           + '[01:22.85]\n'
           + '\n'
           + '[01:25.28]\n'
           + '\n'
           + '[01:27.71]\n'
           + '\n'
           + '[01:29.90]\n'
           + '\n'
           + '[01:32.23]\n'
           + '\n'
           + '[01:34.76]\n'
           + '\n'
           + '[02:01.04]\n'
           + '\n'
           + '[02:03.57]\n'
           + '\n'
           + '[02:05.90]\n'
           + '\n'
           + '[02:10.67]\n'
           + '\n'
           + '[02:13.27]\n'
           + '\n'
           + '[02:15.65]\n'
           + '\n'
           + '[02:20.28]\n'
