           + '[02:39.48]\n'
           + '\n'
           + '[02:41.91]\n'
           + '\n'
           + '[02:44.30]\n'
           + '\n'
           + '[02:47.00]\n'
           + '\n'
           + '[02:49.93]\n'
           + '\n'
           + '[02:51.61]\n'
           + '\n'
           + '[02:54.04]\n'
           + '\n'
           + '[02:58.91]\n'
           + '\n'
           + '[03:01.19]\n'
           + '\n'
           + '[03:03.62]\n'
           + '\n'
           + '[03:06.10]\n'
           + '\n'
    }
  },
  methods: {
    formatTime: function (seconds) {
      return [
        parseInt(seconds / 60 % 60),
        parseInt(seconds % 60)
      ]
        .join(':')
        .replace(/\b(\d)\b/g, '0$1')
    },
    preDrag: function () {
      console.log('sratDrag')
      this.isDrag = true
    },
    endDrag: function () {
      console.log('endDrag')
      this.isDrag = false
    },
    changeTimeLine: function (val) {
      this.au.currentTime = parseInt(val * this.au.duration) / 100
      this.locationLrc(this.au.currentTime)
      if (!this.au.ended) {
        this.isPlaying = true
        this.au.play()
      }
    },
    endPlay: function () {
      this.isPlaying = false
    },
    playerTimeUpdate: function () {
      let currentTime = parseInt(this.au.currentTime)
      let duration = parseInt(this.au.duration)
      this.startTime = this.formatTime(currentTime)
      if (!this.isDrag) {
        this.currentTimeLine = currentTime / duration * 100
      }
      this.updateLrc(currentTime)
    },
    canPlay: function () {
      console.log('this.au', this.au.canPlay)
      this.startTime = this.formatTime(this.au.currentTime)
      this.endTime = this.formatTime(this.au.duration)
    },
    parseLyric: function (lrc) {
      let lyrics = lrc.split('\n')
      let lrcObj = {}
      for (let i = 0; i < lyrics.length; i++) {
        let lyric = decodeURIComponent(lyrics[i])
        let timeReg = /\[\d*:\d*((\.|\:)\d*)*\]/g
        let timeRegExpArr = lyric.match(timeReg)
        if (!timeRegExpArr) continue
        let clause = lyric.replace(timeReg, '')
        for (let k = 0, h = timeRegExpArr.length; k < h; k++) {
          let t = timeRegExpArr[k]
          let min = Number(String(t.match(/\[\d*/i)).slice(1))
          let sec = Number(String(t.match(/\:\d*/i)).slice(1))
          var time = min * 60 + sec
          lrcObj[time] = clause
        }
      }
      this.lrcObj = lrcObj
      console.log(' this.lrcObj', this.lrcObj)
      let propertys = Object.keys(this.lrcObj)
      for (let i = 0; i <= 9; i++) {
        if (i < 5)  {
          this.showLrc.push({no: i, text: ''})
        } else {
          console.log(' push text', this.lrcObj[propertys[i - 5]])
          this.showLrc.push({no: i, text: this.lrcObj[propertys[i - 5]]})
          this.lastTextIndex = i - 5
        }
      }
      console.log(' this.showLrc', this.showLrc)
      this.showText = this.showLrc[5]
    },
    updateLrc: function (currentTime) {
      let curTime = Math.round(currentTime)
      let currentText = this.lrcObj[curTime]
      //      console.log('currentText: ', currentText)
      //      
      if (typeof currentText === 'undefined') return
      //      
      if (currentText !== this.showText) {
        //      
        this.showLrc.shift()
        let propertys = Object.keys(this.lrcObj)
        if (this.lastTextIndex < propertys.length - 1) {
          this.lastTextIndex += 1
          this.showLrc.push({no: this.lastTextIndex + 5, text: this.lrcObj[propertys[this.lastTextIndex]]})
        }
        this.showText = currentText
      }
    },
    locationLrc: function (currentTime) {
      let curTime = Math.round(currentTime)
      let propertys = Object.keys(this.lrcObj)
      let curIndex =  propertys.findIndex((item) => { return item === curTime.toString() })
      while (curIndex === -1) {
        if (curTime === 0) {
          curIndex = 0
          break
        }
        curTime += -1
        curIndex =  propertys.findIndex((item) => { return item === curTime.toString() })
      }
      this.showText = this.lrcObj[curTime]
      this.showLrc = []
      let start = 0
      if (curIndex > 4) {
        start = curIndex - 5
      }
      for (let i = start; i <= start + 9; i++) {
        if (i < 5)  {
          this.showLrc.push({no: i, text: ''})
        } else {
          let offset = (start === 0) ? 5 - start : 0
          this.showLrc.push({no: i, text: this.lrcObj[propertys[i - offset]]})
          this.lastTextIndex = i - offset
        }
      }
    },
    restart: function () {
      this.changeTimeLine(0)
    },
    pausePlayer: function () {
      if (this.isPlaying === true) {
        this.isPlaying = false
        this.au.pause()
      } else {
        this.isPlaying = true
        this.au.play()
      }
    }
  },
  mounted: function () {
    this.au = document.getElementById('audioControl')
    this.au.play()
    this.parseLyric(this.lrc)
  }
}
</script>



