<template>
  <div>
    <header class="header container mt_10" style="height: 0.9rem">
      <div class="search_box ">
        <com-return :title="title" :buttonShow="buttonShow"></com-return>
      </div>
      <div class="clear"></div>
    </header>
    <section class="container ">
      <div class="">
        <ul class="data_list" style="text-align: left">
          <li>
            
            <em class="em_right fr"></em>
            <span class="fr icon_head">
            <img class="b_r" :src="curUser.icon">
          </span>
            <input @change="updateUserIcon" type="file" accept="image/*"/>
          </li>
          <li>
            
            <em class="em_right fr" style="position: relative;right: -.8rem;"></em>
            <span class="fr c_999"
                  style="margin-right: 0.1rem;position: relative;right: -.8rem;">
              {{curUser.nick}}
            </span>
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>
<style>
  .b_r {
    border-radius: 50%;
  }
</style>
<script>/*eslint-disable*/
import ComReturn from '@/components/com_return.vue';
import {mapGetters, mapActions} from 'vuex';
import sv from '@/service/service'
import {Indicator} from 'mint-ui'

export default {
  components: {
    ComReturn
  },
  data() {
    return {
      title: '',
      buttonShow: false
    };
  },
  computed: {
    ...mapGetters(
      {
        curUser: 'currentUser'
      })
  },
  methods: {
    async updateUserInfo(infoNew) {
      let info = await sv.patchUserInfo(infoNew);
      this.setCurUser(info);
    },
    async updateUserIcon({srcElement: el}) {
      let files = el.files;
      if (!files || !files.length) {
        return;
      }
      // console.log(files);
      let {data} = await sv.uploadFile(files);
      let iconUrl = '';
      console.log('', data)
      if (data && data.attributes && data.attributes.f_0) {
        iconUrl = data.attributes.f_0.url;
      }
      console.log('iconUrl', iconUrl)
      await this.updateUserInfo({icon: iconUrl})
    },
    ...mapActions({setCurUser: 'setCurUser'})
  },
  mounted: function () {
  }
};
</script>



