import Axios from 'axios';

const $ = require('jquery');
// let HOST = 'http://192.168.199.107';
// let PORT = ':9080';
let HOST = '//atech.ltd';
let PORT = ':9539';
let base = HOST + PORT;
let requestConfig = {
  headers: {
    'Content-Type': 'application/json',
    'X-Token': localStorage.getItem('token')
  },
  withCredentials: true
};

export default {
  /**
   * 
   */
  autoLogin() {
    let loginStatus = JSON.parse(localStorage.getItem('userStatus')) || false
    if (!loginStatus) {
      let userData = JSON.parse(localStorage.getItem('userData')) || {}
      let param = {
        loginphone: userData.loginphone,
        verifycode: userData.verifycode
      }
      this.login(param)
    }
  },
  /**
   * 
   */
  login(params) {
    return Axios.post(`${base}/login`, params, requestConfig).then(res => {
      localStorage.setItem('token', res.data.meta.token);
      requestConfig.headers['X-Token'] = res.data.meta.token;
      return res.data;
    }, err => {
      console.log('err', err);
    }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  logout(params) {
    return Axios.get(`${base}/logout`, requestConfig).then(res => {
      localStorage.removeItem('token');
      return res.data;
    }, err => {
      console.log('err', err);
    }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getHomeFeatures() {
    return Axios.get(`${base}/catalog`, requestConfig).then(res => {
      return res.data;
    }, err => {
      console.log('err', err);
    }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getSubFeatures(params) {
    return Axios.get(`${base}/catalog/${params}`, requestConfig).then(res => {
      return res.data;
    }, err => {
      console.log('err', err);
    }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getVideoList(params) {
    //console.log('params',Object.assign({ params: params }, requestConfig))
    return Axios.get(
      `${base}/media`, Object.assign({params: params}, requestConfig)).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getVideo(params) {
    return Axios.get(
      `${base}/media/${params.maId}`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getAdvertisement() {
    return Axios.get(
      `${base}/ad`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },

  /**
   * 
   */
  getTags() {
    return Axios.get(
      `${base}/tag`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },

  /**
   * 
   */
  getDictionary(params) {
    return Axios.get(
      `${base}/ModAdapter/Youdao/API`,
      Object.assign({params: params}, requestConfig)
    ).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getPaperList(params) {
    return Axios.get(
      `${base}/paper`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  getPaperTest(params) {
    return Axios.get(
      `${base}/paper/${params}/test`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },

  /**
   * 
   */
  getPaperContent(params) {
    return Axios.get(
      `${base}/paper/${params}`, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },

  /**
   * 
   */
  patchPaperContent(params) {
    return Axios.patch(
      `${base}/test/${params.path}`, params.body, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  async getUserInfo() {
    console.log(' ');
    let {data} = await Axios.get(`${base}/mine/info`, requestConfig);

    let {attributes: user_info} = data.data;
    console.log(' ', user_info);

    return user_info;
  },
  async patchUserInfo(info) {
    console.log('');
    let {data} = await Axios.patch(
      `${base}/mine/info`,
      {
        "data": info
      },
      requestConfig
    );

    let {attributes: user_info} = data.data;
    console.log('', user_info);

    return user_info;
  },
  async uploadFile(files = []) {
    let form_data = new FormData();
    for (let i = 0; i < files.length; i++) {
      let file = files[i];
      form_data.append(`f_${i}`, file);
    }

    let res = await Axios.post(
      `${base}/ModAdapter/Qiniu/upload`,
      form_data,
      requestConfig
    );

    console.log('', res);
    return res.data;
  },
  postUserPic(params) {
    let fileConfig = {
      headers: {
        'Content-type': 'multipart/form-data',
        'token': localStorage.getItem('token')
      },
      withCredentials: true
    };
    console.log(params);
    return Axios.post(`${base}/ModAdapter/Qiniu/upload`, params, fileConfig)
      .then(
        res => {
          return res.data;
        }, err => {
          console.log('err', err);
        })
      .catch((error) => {
        console.log('error', error);
      });
  },
  /**
   * 
   */
  getMyMarkedList(params) {
    //console.log('params',Object.assign({ params: params }, requestConfig))
    return Axios.get(
      `${base}/media`, Object.assign({params: params}, requestConfig)).then(
      res => {
        let myList = res.data.data.filter((item) => {
          return item.attributes.is_marked === '1';
        });
        return myList;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  },
  /**
   * 
   */
  patchMarkedMedia(params) {
    return Axios.patch(
      `${base}/media/${params}`, {meida_id: params}, requestConfig).then(
      res => {
        return res.data;
      }, err => {
        console.log('err', err);
      }).catch((error) => {
      console.log('error', error);
    });
  }
};



